import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Intro() {
  return (
    <div className="ffh-sub ffh-sub--intro" data-sub="intro">
      <section
        className="ffh-section ffh-section--lead"
        data-toc-title="Einleitung"
        data-modalizable="true"
        data-modal-title="Trke  Almanca: Aile, arkada, hobiler"
      >
        <div className="rule-box ffh-rule ffh-rule--intro">
          <strong>Neden Aile, Arkadalar, Hobiler?</strong> Gnlk sohbetin
          %80i bu konulardan oluur. Trkede <em>annem</em>,{" "}
          <em>kardeim</em>, <em>balk tutmay severim</em> gibi ifadelerde
          <Term>eklerle</Term> (-im/-in/-i) anlatrz. Almancada ise bu i ou
          zaman
          <Key>mein/meine</Key> gibi <Term>iyelik szckleri</Term> ve{" "}
          <Key>gern/mgen</Key> kalplaryla yaplr. A1 seviyesinde net cmle
          kalplaryla akc ve hatasz konumay hedefliyoruz.
        </div>

        <Explain
          title="nce genel fark"
          lead="Trkede ek  Almancada ayr kelime"
        >
          <ul className="ffh-ul ffh-ul--intro-points">
            <li>
              <Term>Benim annem</Term> = <Key>meine Mutter</Key> (diil {" "}
              <Key>meine</Key>).
              <Term>Benim babam</Term> = <Key>mein Vater</Key> (eril {" "}
              <Key>mein</Key>).
            </li>
            <li>
              <Term>Sevmek / holanmak</Term>: Almancada gnlk konumada
              <Key>gern + fiil</Key> ok yaygndr:{" "}
              <em>Ich spiele gern Fuball</em>.<Key>mgen</Key> ounlukla
              isimlerle: <em>Ich mag Kaffee</em>.
            </li>
            <li>
              <Term>Tantm</Term> cmleleri ksa tutulur:{" "}
              <Key>Das ist mein Bruder.</Key> /
              <Key>Ich habe eine Schwester.</Key>
            </li>
          </ul>

          <Callout type="info" title="A1 iin hedef kalplar">
            <Formula
              rows={[
                ["Tantma", "Das ist mein(e) ", "Bu benim "],
                [
                  "Sahip olma",
                  "Ich habe einen Bruder / eine Schwester.",
                  "Bir kardeim var.",
                ],
                [
                  "Hobi (fiille)",
                  "Ich + fiil + gern",
                  "Holanrm / severim.",
                ],
                [
                  "Tercih",
                  "Ich + fiil + lieber",
                  "Daha ok tercih ederim.",
                ],
                [
                  "En sevdiim",
                  "Ich + fiil + am liebsten",
                  "En ok severim.",
                ],
              ]}
            />
          </Callout>
        </Explain>

        <Explain title="Mini diyalog" lead="Doal ve ksa bir tanma devam">
          <Examples
            items={[
              {
                en: "A: Hast du Geschwister?  B: Ja, ich habe einen Bruder.",
                tr: "A: Kardein var m?  B: Evet, bir erkek kardeim var.",
                pron: "hast du geviste  ya, ih habe aynn bruda",
              },
              {
                en: "A: Was machst du gern?  B: Ich lese gern und ich spiele Fuball.",
                tr: "A: Ne yapmay seversin?  B: Okumay severim ve futbol oynarm.",
                pron: "vas mahst du gern  ih leze gern und ih piile fubal",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--pron"
        data-toc-title="Aussprache-Hinweise"
      >
        <Explain
          title="Aussprache  aile ve hobi kelimeleri"
          lead="Ksa ama kritik ipular"
        >
          <MiniTable
            head={["Yaz", "Okunu (TR)", "rnek"]}
            rows={[
              ["sp/st (bata)", "p/t", "Sport  port; Strae  trase"],
              [
                "ch (i/e///den sonra)",
                "hi/h",
                "ich  ih/hi; sprechen  preHn",
              ],
              ["r (heceden sonra)", "hafif r", "Bruder  bruda (hafif r)"],
              ["ie", "ii", "spielen  piilen"],
              ["u/eu", "oy", "Freund  froynt; Leute  loyt"],
            ]}
          />
          <Callout type="tip" title="Freund/Freundin dikkat">
            <p>
              <Term>Freund</Term> erkek arkada, <Term>Freundin</Term> kadn
              arkada (romantik arm da olabilir). Ntr ifade iin{" "}
              <Term>Bekannter/Bekannte</Term> (tandk) kullanlabilir (A2de
              ayrnt).
            </p>
          </Callout>
        </Explain>
      </section>
    </div>
  );
}
