import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function BasicCommunicationIntro() {
  return (
    <>
      <div className="rule-box">
        <strong>letiim Hedefleri (Kommunikative Ziele  A2)</strong>: Gnlk
        konular hakknda konuma, gemi deneyimleri <Key>Perfekt</Key> ile
        anlatma, plan/gelecekten sz etme (<Key>Futur I</Key> veya planl{" "}
        <Key>Prsens</Key>), gr + gereke (<Key>dass, weil, denn</Key>),
        nazik istek ve basit formlar.
      </div>

      <Explain
        title="A2de iletiim resmi (Was kann ich auf A2?)"
        lead="Hedef: gnlk hayat anlatmak, planlar paylamak, basit gerekeler vermek ve nazike ricada bulunmak."
      >
        <p>
          A2 seviyesinde ama, <strong>tandk durumlar</strong> hakknda net ve
          basit cmlelerle konumaktr. Bunun iin temel zamanlar (
          <Term>Prsens, Perfekt, Futur I</Term>), cmle balar (
          <Term>weil, dass, denn</Term>) ve <Term>nazik kalplar</Term>
          kullanrz.
        </p>
      </Explain>

      <Sub title="Gnlk konular: ana kelimeler ve kalplar (Alltagsthemen  Kernvokabeln & Chunks)" />
      <Explain
        title="Konular nasl ele alrm? (Wie spreche ich ber Alltag?)"
        lead="Her tema iin 3 ey: temel isimler, tipik fiiller, hazr kalplar."
      >
        <MiniTable
          head={["Konu", "Hazr kalp (TR eviri)", "rnek cmle"]}
          rows={[
            [
              "Ev & Yaam (Wohnen)",
              "Ich wohne in  (de yayorum)",
              "Ich wohne in Kln, in einer kleinen Wohnung.",
            ],
            [
              " & Meslek (Arbeit)",
              "Ich arbeite als  ( olarak alyorum)",
              "Ich arbeite als Verkuferin in einem Geschft.",
            ],
            [
              "Okul & Ders (Schule/Studium)",
              "Ich gehe auf  ( okuluna gidiyorum)",
              "Mein Sohn geht auf die Grundschule.",
            ],
            [
              "Alveri & Hizmet (Einkaufen/Service)",
              "Ich brauche  (a ihtiyacm var)",
              "Ich brauche heute frisches Gemse.",
            ],
            [
              "Seyahat & Ulam (Reisen/Verkehr)",
              "Ich fahre nach  (e gidiyorum)",
              "Wir fahren morgen nach Berlin.",
            ],
            [
              "Salk (Gesundheit)",
              "Ich habe  (im var)",
              "Ich habe Kopfschmerzen und einen Termin beim Arzt.",
            ],
          ]}
        />
      </Explain>

      <Sub title="Gemii anlatma (Vergangenheit  Perfekt)" />
      <Explain
        title="Ne oldu? Ne yaptn? (Wie erzhle ich von gestern?)"
        lead="Konumada gemi iin genelde Perfekt: <Key>haben/sein + Partizip II</Key>."
      >
        <Formula
          rows={[
            [
              "Olumlu",
              "zne + haben/sein +  + Partizip II",
              "Ich habe gearbeitet.  Er ist spt gekommen.",
            ],
            [
              "Olumsuz",
              "zne + haben/sein +  + nicht/kein + Partizip II",
              "Wir haben heute nicht gelernt.",
            ],
            [
              "Soru",
              "Haben/Sein + zne +  + Partizip II?",
              "Hast du am Wochenende gearbeitet?",
            ],
          ]}
        />
        <Examples
          items={[
            {
              en: "Am Wochenende habe ich meine Familie besucht.",
              tr: "Hafta sonu ailemi ziyaret ettim.",
              pron: "am voknende habe ih mayn familie bezu:ht",
            },
            {
              en: "Gestern ist sie frh eingeschlafen.",
              tr: "Dn o erken uykuya dald.",
              pron: "gestan ist zi fryu ayn-gelafen",
            },
          ]}
        />
      </Explain>

      <Sub title="Gelecek & planlar (Zukunft & Plne  Futur I / Prsens)" />
      <Explain
        title="Ne yapacaksn? (Wie spreche ich ber Zukunft?)"
        lead="Planl gelecek iin <Key>Prsens + zaman zarf</Key>, tahmin/niyet iin <Key>Futur I: werden + Infinitiv</Key>."
      >
        <MiniTable
          head={["Yap", "Kalp", "rnek"]}
          rows={[
            [
              "Planl Prsens",
              "Prsens + zaman",
              "Morgen <b>gehe</b> ich zum Zahnarzt.",
            ],
            [
              "Futur I",
              "werden + Infinitiv",
              "Ich <b>werde</b> nchstes Jahr Deutsch <b>lernen</b>.",
            ],
          ]}
        />
        <Callout type="tip" title="Ne zaman hangisi?">
          <p>
            <Term>Bugn/Yarn/Haftaya</Term> gibi net planlarda Prsens ok
            doal: <em>Am Freitag fahre ich nach Mnchen.</em>
          </p>
          <p>
            <Term>nanma/tahmin/niyet</Term> vurgusunda Futur I:{" "}
            <em>Es wird bald regnen.</em>
          </p>
        </Callout>
        <Examples
          items={[
            {
              en: "Morgen treffe ich meine Freunde.",
              tr: "Yarn arkadalarmla buluuyorum.",
              pron: "morgen tref ih mayn froynde",
            },
            {
              en: "Ich werde mehr Sport machen.",
              tr: "Daha fazla spor yapacam.",
              pron: "ih verde mea port mahn",
            },
          ]}
        />
      </Explain>

      <Sub title="Gr + gereke (Meinung + Begrndung: dass/weil/denn)" />
      <Explain
        title="Neden yle dnyorsun? (Wie begrnde ich?)"
        lead="Gr: <Key>ich finde/denke, dass </Key>. Gereke: <Key>weil</Key> yan cmle, <Key>denn</Key> ana cmle."
      >
        <MiniTable
          head={["Balant", "Kalp", "rnek"]}
          rows={[
            [
              "Gr + ierik",
              "Ich finde, dass ",
              "Ich finde, dass dieser Kurs sehr gut ist.",
            ],
            [
              "Sebep (yan cmle)",
              ", weil ",
              "Ich bleibe zu Hause, weil ich krank bin.",
            ],
            [
              "Sebep (ana cmle)",
              ", denn ",
              "Ich bleibe zu Hause, denn ich bin krank.",
            ],
          ]}
        />
        <Examples
          items={[
            {
              en: "Ich denke, dass Deutsch wichtig ist.",
              tr: "Almancann nemli olduunu dnyorum.",
              pron: "ih denke, das doy vihtiH ist",
            },
            {
              en: "Wir fahren nicht, denn es ist zu teuer.",
              tr: "Gitmiyoruz, nk ok pahal.",
              pron: "via farn niHt, den es ist tsu toya",
            },
          ]}
        />
      </Explain>

      <Sub title="Nazik istek & formlar (Hfliche Bitten & Formulare)" />
      <Explain
        title="Rica nasl yaplr? (Wie bitte ich hflich?)"
        lead="Kalplar: <Key>Knnten Sie ?</Key>  <Key>Wrden Sie ?</Key>  <Key>Drfte ich ?</Key>"
      >
        <MiniTable
          head={["Durum", "Kalp", "rnek cmle"]}
          rows={[
            [
              "Yardm isteme",
              "Knnten Sie ?",
              "Knnten Sie mir bitte helfen?",
            ],
            [
              "Randevu/istek",
              "Wrden Sie ?",
              "Wrden Sie mir einen Termin geben?",
            ],
            ["zin sorma", "Drfte ich ?", "Drfte ich hier sitzen?"],
          ]}
        />
        <Callout type="info" title="Form doldurma kelimeleri">
          <p>
            <Term>
              Name, Adresse, Geburtsdatum, Unterschrift, Termin, Formular
            </Term>{" "}
            gibi kelimeler A2de sk geer.
          </p>
        </Callout>
        <Examples
          items={[
            {
              en: "Knnten Sie das Formular erklren?",
              tr: "Formu aklayabilir misiniz?",
              pron: "kntn zi das formulare erklren",
            },
            {
              en: "Drfte ich mit Karte bezahlen?",
              tr: "Kartla deyebilir miyim?",
              pron: "drfte ih mit karte betsaln",
            },
          ]}
        />
      </Explain>

      <Sub title="Mikro altrmalar (Mini-bungen)" />
      <ExerciseFill
        title="Gemi  Gelecek  Gereke"
        items={[
          {
            id: "bc1",
            before: "Gestern ____ ich meine Freunde ________ (treffen).",
            after: "",
            answers: ["habe getroffen"],
          },
          {
            id: "bc2",
            before: "Morgen ____ ich zum Arzt. (gehen  planl Prsens)",
            after: "",
            answers: ["gehe"],
          },
          {
            id: "bc3",
            before: "Ich bleibe zu Hause, ___ ich mde bin.",
            after: "",
            answers: ["weil"],
          },
          {
            id: "bc4",
            before: "___ Sie mir bitte helfen?",
            after: "",
            answers: ["Knnten"],
          },
          {
            id: "bc5",
            before: "Ich denke, ___ Deutsch wichtig ist.",
            after: "",
            answers: ["dass"],
          },
        ]}
      />
      <ExerciseMC
        title="Hzl kontrol"
        items={[
          {
            q: "Planl gelecek iin en doal cmle hangisi?",
            choices: [
              "Morgen werde ich ins Bro gehen.",
              "Morgen gehe ich ins Bro.",
              "Ich bin morgen ins Bro gegangen.",
            ],
            correctIndex: 1,
            explain: "Plan + zaman zarf  Prsens: Morgen gehe ich ",
          },
          {
            q: "Hangi cmlede gereke yan cmleyle verilir?",
            choices: [
              "Ich bleibe zu Hause, denn ich bin krank.",
              "Ich bleibe zu Hause, weil ich krank bin.",
              "Ich bleibe zu Hause, ich bin krank.",
            ],
            correctIndex: 1,
            explain: "weil  yan cmlede fiil sonda:  weil ich krank bin.",
          },
          {
            q: "Nazik rica kalb hangisi?",
            choices: [
              "Ich will Hilfe.",
              "Hilf mir!",
              "Knnten Sie mir bitte helfen?",
            ],
            correctIndex: 2,
            explain: "A2de nazik yap: Knnten Sie  bitte ?",
          },
        ]}
      />

      <Callout type="tip" title="Mini yazma grevi (4060 kelime)">
        <p>
          Hafta sonun hakknda yaz: Ne yaptn (<Term>Perfekt</Term>)? Yarn iin
          plann ne (<Term>Prsens/Futur I</Term>)? Gr + neden ekle (
          <Term>dass/weil</Term>). En az iki nazik rica rnei kullan.
        </p>
      </Callout>
    </>
  );
}
