import React from "react";
import Explain from "../../../../../../../components/Explain";
import { MiniTable, Term } from "../../../../../blocks";

const PRON_HINTS = [
  ["ei", "ay", "heit  hayst; mein  mayn"],
  ["ie", "ii", "Wie  vii; viele  fiile"],
  ["eu/u", "oy", "Freund  froynd; Huser  hoyzr"],
  ["ch (i/e)", "yumuak h/hi", "ich  ih/hi; richtig  riHtiH"],
  ["ch (a/o/u)", "boaz h/kh", "Buch  buh/kh; Nacht  naHt"],
  ["s / sp / st", "z / p / t", "Sie  zi; Strae  traase"],
];

export default function Pronunciation() {
  return (
    <div className="gi-sub gi-sub--pronunciation" data-sub="pronunciation">
      <Explain
        title="Aussprache  Trke odakl hzl rehber"
        lead="ei/ie, eu/u ve ch seslerini erkenden doru oturtmak, hem konuman hem de dinleme-anlaman ok hzlandrr."
      >
        <p className="gi-paragraph" data-modalizable="true">
          Kelime bandaki <Term>s</Term> ou zaman z gibi okunur (
          <em>Sie  zi</em>). <Term>eu/u</Term> daima oydur.
          <Term>ch</Term> sesi iki farkldr: i/e sonras yumuak, a/o/u sonras
          boaza yakn.
        </p>
        <div className="gi-table gi-table--pron">
          <MiniTable
            head={["Yaz", "Okunu (TR)", "rnekler"]}
            rows={PRON_HINTS}
          />
        </div>
      </Explain>
    </div>
  );
}
