import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Intro() {
  return (
    <div className="ntd-sub ntd-sub--intro" data-sub="intro">
      <section
        className="ntd-section ntd-section--lead"
        data-toc-title="Einleitung"
      >
        <div
          className="rule-box ntd-rule ntd-rule--intro"
          data-modalizable="true"
          data-modal-title="Kontext: Trke vs. Almanca"
        >
          <strong>Zahlen, Uhrzeit, Datum  neden nemli?</strong> Gnlk hayatta
          her yerde karna kar: randevu saati, bilet numaras, fiyatlar,
          tarih. Trkede bazen tek bir kalp birok durumu anlatr. Almancada
          ise <Term>biim</Term> ve <Term>kk kelimeler</Term> (rn.{" "}
          <em>am, um, im</em>) net ve dzenlidir. Bu blmde Trkede byle
          deriz ama Almancada yledir farklarn anlalr bir ekilde
          kuruyoruz.
        </div>

        <Explain
          title="Genel farklar  hzl bak"
          lead="Trke  Almanca arasnda rencilerin en ok kartrd noktalar:"
        >
          <ul className="ntd-ul ntd-ul--intro-points">
            <li>
              <Term>24 saat</Term> format Almancada ok yaygn: <em>13:30</em>{" "}
               <em>dreizehn Uhr dreiig</em> / <em>halb zwei</em>.
            </li>
            <li>
              <Term>halb</Term> Almancada <strong>sonraki</strong> saate yarm
              kala demek: <Key>halb neun</Key> = <strong>08:30</strong>,
              (Trkede dokuz buuk 9:30dur  kartrma!).
            </li>
            <li>
              <Term>Virgl</Term> ondalk ayrr: <Key>3,5 </Key> ={" "}
              <em>drei Euro fnfzig</em> (nokta deil!).
            </li>
            <li>
              <Term>Tarih</Term> nce gn gelir: <Key>31.12.2025</Key> ve
              okunuta sra says: <em>am einunddreiigsten Dezember</em>.
            </li>
          </ul>

          <Callout type="info" title="Notasyon  yazarken ve okurken">
            <ul className="compact-list">
              <li>
                <strong>Binlik</strong> ayrc: <em>1.000</em> (nokta) ya da{" "}
                <em>1 000</em> (boluk).
              </li>
              <li>
                <strong>Ondalk</strong>: <em>2,75</em> (virgl). Fiyatlar:{" "}
                <em>2,75 </em>  <em>zwei Euro fnfundsiebzig</em>.
              </li>
              <li>
                <strong>Tarih</strong>: <em>Tag.Monat.Jahr</em> (31.05.2025).
              </li>
            </ul>
          </Callout>
        </Explain>

        <Explain
          title="Sk sorulara hzl yant"
          lead="Aadaki kalplar A1 iin %90 yeterlidir."
        >
          <Formula
            rows={[
              [
                "Saat sorma",
                "Wie spt ist es? / Wie viel Uhr ist es?",
                "Saat ka?",
              ],
              [
                "Saat syleme",
                "Es ist 8 Uhr / Es ist halb neun",
                "Saat 8 / 8 buuk (08:30)",
              ],
              ["Randevu", "um 9 Uhr", "saat 9da"],
              [
                "Tarih",
                "am 3. Mai / am dritten Mai",
                "3 Maysta",
              ],
              [
                "Ay/Yl",
                "im Januar / im Jahr 2025",
                "Ocakta / 2025 ylnda",
              ],
            ]}
          />
        </Explain>

        <Explain title="Mini diyalog" lead="Ksa, doal bir konuma">
          <Examples
            items={[
              {
                en: "A: Wie viel Uhr ist es?  B: Es ist halb neun.",
                tr: "A: Saat ka?  B: Sekiz buuk.",
                pron: "vi fil uua ist es  es ist halp noyN",
              },
              {
                en: "A: Heute ist der 5. Juni, oder?  B: Ja, am fnften Juni.",
                tr: "A: Bugn 5 Haziran, deil mi?  B: Evet, 5 Haziranda.",
                pron: "hoyt ist dea fnfte yuni, oda  ya, am fnften yuni",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--pron"
        data-toc-title="Aussprache-Hinweise"
      >
        <Explain
          title="Aussprache  Zahlen iin kritik sesler"
          lead="Doru ses  doru anlama."
        >
          <MiniTable
            head={["Yaz", "Okunu (TR)", "rnek"]}
            rows={[
              ["z", "ts", "zehn  tsen; zwanzig  tsvansih"],
              ["w", "v", "zwei  tsvay; zwlf  tsvlf"],
              ["v", "f", "vier  fiir; fnf  fnf"],
              ["j", "y", "Juni  yuni; Januar  yanua"],
              ["u/eu", "oy", "neun  noyn; zwlf  tsvlf ( sesi)"],
              ["/ss", "s", "dreiig  draysih; Klasse  klase"],
            ]}
          />
          <Callout type="tip" title="Hzl dzeltme">
            <p>
              <Term>z</Term> harfini asla z gibi syleme; <strong>ts</strong>{" "}
              yap: <em>zehn  tsen</em>.
            </p>
          </Callout>
        </Explain>
      </section>
    </div>
  );
}
