import React from "react";
import { Link } from "react-router-dom";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Hobbies() {
  return (
    <div className="ffh-sub ffh-sub--hobbies" data-sub="hobbies">
      <section
        className="ffh-section ffh-section--gern"
        data-toc-title="gern / mgen / lieber"
      >
        <Explain
          title="Holanma ve tercih"
          lead="Gnlk dilde severim iin en doal kalp <Key>gern</Key>dir (fiille). <Key>mgen</Key> ise ounlukla isimlerle."
        >
          <Formula
            rows={[
              [
                "Holanrm",
                "Ich + fiil + gern",
                "Ich schwimme gern.",
              ],
              [
                "Severim (isimle)",
                "Ich mag Kaffee.",
                "Kahveyi severim.",
              ],
              [
                "Daha ok severim",
                "Ich + fiil + lieber",
                "Ich koche lieber.",
              ],
              [
                "En ok severim",
                "Ich + fiil + am liebsten",
                "Ich reise am liebsten.",
              ],
              [
                "Sevmem / holanmam",
                "nicht gern",
                "Ich tanze nicht gern.",
              ],
            ]}
          />
          <Callout type="warning" title="Hata uyars">
            <p>
              <Key>*Ich gern schwimme</Key> deil {" "}
              <strong>Ich schwimme gern</strong> (gern her zaman fiille
              birlikte).
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--verbs"
        data-toc-title="Hobi fiilleri"
      >
        <Explain title="Sk fiiller" lead="A1de en ok bunlar kullanlr">
          <MiniTable
            head={["Fiil", "Trke", "rnek cmle"]}
            rows={[
              ["spielen", "oynamak (spor/oyun)", "Ich spiele Fuball/Karten."],
              ["machen", "yapmak", "Ich mache Fotos."],
              ["gehen", "gitmek", "Ich gehe joggen / ins Kino."],
              ["lesen", "okumak", "Ich lese gern Bcher."],
              ["kochen/backen", "yemek/piirmek", "Ich koche gern Pasta."],
              ["schwimmen", "yzmek", "Ich schwimme im Sommer gern."],
              ["Musik hren", "mzik dinlemek", "Ich hre gern Musik."],
              ["fernsehen", "televizyon izlemek", "Ich sehe abends fern."],
            ]}
          />
          <Callout type="tip" title="Ayrlabilen fiil rnei">
            <p>
              <Term>fernsehen</Term> ayrlabilir: <Key>Ich sehe</Key> abends{" "}
              <Key>fern</Key>. Ayrnts Trennbare Verben blmnde.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialogs"
        data-toc-title="Diyaloglar"
      >
        <Explain title="Ksa diyaloglar" lead="Holanma ve tercihleri syle">
          <Examples
            items={[
              {
                en: "Was machst du in deiner Freizeit?  Ich spiele gern Volleyball.",
                tr: "Bo zamannda ne yaparsn?  Voleybol oynamay severim.",
                pron: "vas mahst du in dayna fraayt-tsayt  ih piile gern voleybal",
              },
              {
                en: "Magst du Kaffee?  Ja, aber ich trinke lieber Tee.",
                tr: "Kahveyi sever misin?  Evet, ama ay daha ok tercih ederim.",
                pron: "makst du kafe  ya, aba ih trinke li:ba tee",
              },
              {
                en: "Ich reise am liebsten im Sommer.",
                tr: "En ok yazn seyahat etmeyi severim.",
                pron: "ih rayz am li:pstn im zomma",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--cta"
        data-toc-title="Mini-Test balants"
      >
        <Callout type="tip" title="Hazr msn?">
          <p>Bu nitenin mini testini ayr sayfada zmek iin tkla:</p>
          <p>
            <Link className="quiz-link btn" to="../quiz">
              Mini-Teste git
            </Link>
          </p>
        </Callout>
      </section>
    </div>
  );
}
