import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function RelativeClausesAdvanced() {
  return (
    <>
      <div className="rule-box">
        <strong>leri Relative Clauses</strong>: Defining / Non-defining,
        which ile <em>tm cmleyi</em> niteleme, edatla bitirme/ne alma,
        ksaltma.
      </div>

      <Sub title="Defining vs Non-defining (virgl fark)" />
      <MiniTable
        head={["Tr", "rnek", "Not"]}
        rows={[
          [
            "Defining",
            "The students who live in Ankara are here.",
            "Kimlerden bahsettiimizi belirler; virgl yok.",
          ],
          [
            "Non-defining",
            "My brother, who lives in Ankara, is here.",
            "Ek bilgi; iki virgl arasnda.",
          ],
        ]}
      />

      <Sub title="Edatlar: preposition + which/whom" />
      <MiniTable
        head={["Desen", "rnek", "Stil"]}
        rows={[
          ["end-placed", "the topic which I told you about", "Konuma dili"],
          ["fronted", "the topic about which I told you", "Resmi/yazl"],
          ["people", "the person to whom I spoke", "who yerine whom (resmi)"],
        ]}
      />

      <Sub title="which tm cmleyi nitelediinde" />
      <Examples
        items={[
          {
            en: "He refused to comment, which surprised everyone.",
            tr: "Yorum yapmay reddetti; bu da herkesi artt.",
            pron: "hi rif-yuuzd t koment, vi sarp-rayzd evrywn",
          },
          {
            en: "She passed the exam, which made her family proud.",
            tr: "Snav geti  bu da ailesini gururlandrd.",
            pron: "ii past di egzm, vi meyd hr femli praud",
          },
        ]}
      />
      <Callout type="tip" title="Trkeye eviri">
        Bu <Key>which</Key>, sondaki <em>bu da / bu ise</em> anlamn verir;
        nceki tm cmleye referans yapar.
      </Callout>

      <Sub title="Ksaltma (reduction) tekrar" />
      <MiniTable
        head={["Tam", "Ksa", "Not"]}
        rows={[
          ["people who are working", "people working", "be + V-ing der"],
          ["results that were expected", "results expected", "pasif ksaltma"],
          [
            "the first person who entered",
            "the first person to enter",
            "to-infinitive (ama/gelecek)",
          ],
        ]}
      />

      <Sub title="Altrma  Gap-fill" />
      <ExerciseFill
        title="Relative Advanced"
        items={[
          {
            id: "rc1",
            before: "My thesis, ___ was finally approved, took two years.",
            after: "",
            answers: ["which"],
          },
          {
            id: "rc2",
            before: "The woman to ___ I spoke was the director.",
            after: "",
            answers: ["whom"],
          },
          {
            id: "rc3",
            before:
              "The city ___ I grew up is by the sea. (end-placed preposition)",
            after: "",
            answers: ["which", "that"],
          },
          {
            id: "rc4",
            before:
              "The first student ___ submit the form will get a bonus. (to-infinitive)",
            after: "",
            answers: ["to"],
          },
          {
            id: "rc5",
            before: "Employees ___ (work) remotely must log in by 9.",
            after: "",
            answers: ["working"],
          },
        ]}
      />

      <ExerciseMC
        title="Mini Test"
        items={[
          {
            q: "Non-defining clause iin hangisi DORU?",
            choices: [
              "Virgl yoktur.",
              "Virgl gerekir ve that kullanlmaz.",
              "that zorunludur.",
            ],
            correctIndex: 1,
            explain: "Non-defining: virgl gerekir; that kullanlmaz.",
          },
          {
            q: "which tm cmleyi nitelediinde ilevi nedir?",
            choices: [
              "Nesneyi belirtir.",
              "nceki tm cmleye bu da/ki bu anlam katar.",
              "Sadece zaman belirtir.",
            ],
            correctIndex: 1,
            explain: "nceki cmleye referans veren bala grevi grr.",
          },
        ]}
      />
    </>
  );
}
