import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function LinkersContrast() {
  return (
    <>
      <div className="rule-box">
        <strong>Kartlk Balalar</strong>:{" "}
        <Key>although / though / even though</Key> (cmle) ve
        <Key>despite / in spite of</Key> (isim/gerund). Yazda ayrca{" "}
        <Key>however / nevertheless</Key>.
      </div>

      <Sub title="Cmle balayanlar" />
      <MiniTable
        head={["Bala", "Yap", "rnek"]}
        rows={[
          [
            "although / though",
            "Although + S + V, ...",
            "Although it was late, we kept working.",
          ],
          [
            "even though",
            "Even though + S + V, ...",
            "Even though I was tired, I went running.",
          ],
          [
            "however",
            "S + V. However, S + V.",
            "It rained. However, we went out.",
          ],
        ]}
      />
      <Callout type="tip" title="Trke karlk">
        <em>Although/even though</em>  -e ramen; <em>however</em>  ancak,
        yine de (bamsz cmleyi balar).
      </Callout>

      <Sub title="sim/fiil-ing ile kullanlanlar" />
      <MiniTable
        head={["Bala", "Yap", "rnek"]}
        rows={[
          ["despite", "Despite + noun / V-ing", "Despite the rain, we played."],
          [
            "in spite of",
            "In spite of + noun / V-ing",
            "In spite of being busy, she called.",
          ],
          [
            "despite the fact that",
            "Despite the fact that + clause",
            "Despite the fact that it was late, ...",
          ],
        ]}
      />

      <Sub title="nce farklar & stil" />
      <Callout type="info" title="Stil notu">
        <ul className="compact-list">
          <li>
            <Key>even though</Key>  daha gl kartlk.
          </li>
          <li>
            <Key>though</Key> konumada sk, cmle <em>sonunda</em> da
            gelebilir: I liked it, though.
          </li>
          <li>
            <Key>however</Key> resmi yazda; <Key>but</Key> konumada.
          </li>
        </ul>
      </Callout>

      <Sub title="rnekler" />
      <Examples
        items={[
          {
            en: "Although the task was complex, the team delivered.",
            tr: "Grev karmak olmasna ramen ekip teslim etti.",
            pron: "olthough d tesk woz kompleks, d tiim dilivrd",
          },
          {
            en: "In spite of the noise, he managed to sleep.",
            tr: "Grltye ramen uyumay baard.",
            pron: "in spayt ov d noyz, hii menet t sliip",
          },
          {
            en: "We were tired; however, we continued.",
            tr: "Yorgunduk; ancak devam ettik.",
            pron: "vii vr tayrd; hav-evr, vii kontin-yuud",
          },
          {
            en: "Despite being new, she performed well.",
            tr: "Yeni olmasna ramen iyi performans gsterdi.",
            pron: "dispait biiyi nyuu, ii prformd uel",
          },
        ]}
      />

      <Sub title="Altrma  Gap-fill" />
      <ExerciseFill
        title="Contrast Linkers"
        items={[
          {
            id: "lk1",
            before: "___ it was snowing, the match went on.",
            after: "",
            answers: ["Although", "Even though"],
          },
          {
            id: "lk2",
            before: "___ the pressure, they delivered on time.",
            after: "",
            answers: ["Despite", "In spite of"],
          },
          {
            id: "lk3",
            before: "She was ill; ___, she attended the class.",
            after: "",
            answers: ["however", "nevertheless"],
          },
          {
            id: "lk4",
            before: "___ being warned, he took the risk.",
            after: "",
            answers: ["Despite", "In spite of"],
          },
          {
            id: "lk5",
            before: "___ it was late, we stayed to help.",
            after: "",
            answers: ["Even though", "Although"],
          },
        ]}
      />

      <ExerciseMC
        title="Mini Test"
        items={[
          {
            q: "Hangisi isimle kullanlr?",
            choices: ["although", "despite", "even though"],
            correctIndex: 1,
            explain: "despite + isim/V-ing.",
          },
          {
            q: "Hangi seenek daha gl kartlk verir?",
            choices: ["although", "even though", "though (sonda)"],
            correctIndex: 1,
            explain: "even though en gl vurgudur.",
          },
        ]}
      />
    </>
  );
}
