import React from "react";
import { Link } from "react-router-dom";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

const DAYS = [
  ["Montag", "Pazartesi", "montak"],
  ["Dienstag", "Sal", "diinstak"],
  ["Mittwoch", "aramba", "mitvoh"],
  ["Donnerstag", "Perembe", "donastak"],
  ["Freitag", "Cuma", "fraytak"],
  ["Samstag / Sonnabend", "Cumartesi", "zamstak / zonabnt"],
  ["Sonntag", "Pazar", "zontak"],
];

const MONTHS = [
  ["Januar", "Ocak", "yanuar"],
  ["Februar", "ubat", "februar"],
  ["Mrz", "Mart", "merts"],
  ["April", "Nisan", "april"],
  ["Mai", "Mays", "may"],
  ["Juni", "Haziran", "yuni"],
  ["Juli", "Temmuz", "yuli"],
  ["August", "Austos", "august"],
  ["September", "Eyll", "zeptemba"],
  ["Oktober", "Ekim", "oktooba"],
  ["November", "Kasm", "novemba"],
  ["Dezember", "Aralk", "detsemba"],
];

export default function DatePage() {
  return (
    <div className="ntd-sub ntd-sub--date" data-sub="date">
      <section
        className="ntd-section ntd-section--format"
        data-toc-title="Tarih biimi & okunu"
      >
        <Explain
          title="Tarih nasl yazlr ve okunur?"
          lead="Biim: <Key>Tag.Monat.Jahr</Key>  31.05.2025. Okurken gn iin sra says kullanlr."
        >
          <Formula
            rows={[
              ["Yazm", "31.05.2025", "31 Mays 2025"],
              ["Okunu (ksa)", "am 31.5.2025", "31.05.2025te"],
              [
                "Okunu (tam)",
                "am einunddreiigsten Mai 2025",
                "31 Mays 2025te",
              ],
            ]}
          />
          <Callout type="info" title="Sra says (1.31.)">
            <p>
              1.19. iin <strong>-te</strong>, 20. ve sonras iin{" "}
              <strong>-ste</strong>:{" "}
              <em>
                der erste, der zweite, der dritte  der zwanzigste, der
                einundzwanzigste
              </em>
              . Tarihte genelde <Term>am</Term> + <Term>ten/-sten</Term>{" "}
              kullanlr: <Key>am dritten Mai</Key>.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--days"
        data-toc-title="Gnler & aylar"
      >
        <Explain
          title="Gnler  hafta Pazartesi balar"
          lead="Almancada takvim haftas Pazartesi ile balar (Montag)."
        >
          <MiniTable head={["Almanca", "Trke", "Okunu (TR)"]} rows={DAYS} />
        </Explain>

        <Explain
          title="Aylar"
          lead="Juni/Juli ayrmna dikkat: ikisi de y ile balar."
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={MONTHS}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--preps"
        data-toc-title="Edatsal kalplar (am/um/im)"
      >
        <Explain
          title="Doru edat se"
          lead="Zaman ifadelerinde kk kelimeler anlam belirler."
        >
          <MiniTable
            head={["Kalp", "Anlam", "rnek"]}
            rows={[
              ["am + Gn/Tarih", "da (gn/tarihler)", "am Montag; am 3. Mai"],
              ["um + Saat", "da (saat)", "um 8 Uhr"],
              [
                "im + Ay/Yl/Mevsim",
                "de/da (zaman aral)",
                "im Januar; im Jahr 2025; im Sommer",
              ],
              [
                "am Wochenende",
                "Hafta sonu",
                "am Wochenende arbeiten wir nicht.",
              ],
            ]}
          />
          <Callout type="warning" title="Karklk uyars">
            <p>
              <Term>am</Term> gn/tarih iindir; <Term>um</Term> saat iindir;{" "}
              <Term>im</Term> ay/yl/mevsim iindir.
              <Key>*im Montag</Key> veya <Key>*am 8 Uhr</Key> hataldr.
            </p>
          </Callout>
        </Explain>

        <Explain title="Diyaloglar" lead="Doal rnekler">
          <Examples
            items={[
              {
                en: "Der Termin ist am 12. Oktober um 14 Uhr.",
                tr: "Randevu 12 Ekim saat 14:00te.",
                pron: "dea termin ist am zlf oktooba um fiat tseyn uua",
              },
              {
                en: "Im Juli haben wir Urlaub.",
                tr: "Temmuzda iznimiz var.",
                pron: "im yuli habn viA ualaop",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--cta"
        data-toc-title="Mini-Test balants"
      >
        <Callout type="tip" title="Kontrol zaman">
          <p>Hazrsan mini teste ge:</p>
          <p>
            <Link className="quiz-link btn" to="../quiz">
              Mini-Teste git
            </Link>
          </p>
        </Callout>
      </section>
    </div>
  );
}
