import React from "react";
import { Link } from "react-router-dom";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Directions() {
  return (
    <div className="had-sub had-sub--directions" data-sub="directions">
      <section
        className="had-section had-section--ask"
        data-toc-title="Yol sorma"
      >
        <Explain
          title="Yol sorma kalplar"
          lead="Kibar bala, basit istekte bulun."
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "Entschuldigung, wo ist ?",
                "Afedersiniz,  nerede?",
                "ent-uldigung, vo ist ",
              ],
              [
                "Wie komme ich zu(r/m) ?",
                "a/e nasl giderim?",
                "vi kome ih tsu(r/m) ",
              ],
              [
                "Knnen Sie mir helfen?",
                "Bana yardm eder misiniz?",
                "knnn zi mia helfn",
              ],
            ]}
          />
          <Callout type="info" title="zur / zum nedir?">
            <p>
              <Term>zu der  zur</Term>, <Term>zu dem  zum</Term>. A1de kalp
              olarak ren:
              <Key>zur Post</Key>, <Key>zum Bahnhof</Key>.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="had-section had-section--give"
        data-toc-title="Yol tarif etme"
      >
        <Explain title="Temel ynergeler" lead="Sa/sol, dz, ke, klar">
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              ["geradeaus", "dz", "gara-deaus"],
              ["links / rechts", "sol / sa", "links / reHts"],
              ["an der Ecke", "kede", "an dea eke"],
              ["an der Ampel", "trafik klarnda", "an dea ampel"],
              ["ber die Strae", "soka kardan karya", "yba di trase"],
              [
                "die zweite Strae links",
                "ikinci sokaktan sola",
                "di tsvayte trase links",
              ],
              ["gegenber", "karsnda", "gegen-b"],
              [
                "neben / hinter / vor",
                "yannda / arkasnda / nnde",
                "neben / hinta / foa",
              ],
            ]}
          />
          <Examples
            items={[
              {
                en: "Gehen Sie geradeaus, dann an der Ampel rechts.",
                tr: "Dz gidin, sonra klardan saa dnn.",
                pron: "ge-en zi gara-deaus, dan an dea ampel reHts",
              },
              {
                en: "Die Apotheke ist gegenber der Bank.",
                tr: "Eczane bankann karsnda.",
                pron: "di apote-ke ist gegen-b dea bank",
              },
            ]}
          />
        </Explain>

        <Explain
          title="Ksa yol + ulam"
          lead="Yryerek veya toplu tamayla"
        >
          <MiniTable
            head={["Almanca", "Trke", "Not"]}
            rows={[
              ["zu Fu", "yaya", ""],
              [
                "mit dem Bus/der U-Bahn",
                "otobsle/metro ile",
                "Linie 3, U8 vb.",
              ],
              ["aussteigen / umsteigen", "inmek / aktarma yapmak", ""],
              ["Haltestelle", "durak", ""],
              ["Fahrkarte", "bilet", ""],
            ]}
          />
          <Examples
            items={[
              {
                en: "Sie fahren mit der U8 und steigen am Hermannplatz aus.",
                tr: "U8 ile gidiyorsunuz ve Hermannplatzda iniyorsunuz.",
                pron: "zi fah-rn mit dea u-acht und taygn am heamanplats aus",
              },
            ]}
          />
          <Callout type="warning" title="in ile yn/mekn fark">
            <p>
              <Term>in</Term> bazen nereye? anlam verir:{" "}
              <Key>in die Apotheke gehen</Key> (eczahaneye gitmek); bazen de
              nerede?: <Key>in der Apotheke sein</Key> (eczahanede). A1de
              anlam cmleden kar; ayrntlarn <em>Kasus</em> blmnde
              ileyeceiz.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="had-section had-section--mini-dialogs"
        data-toc-title="Diyalog rnekleri"
      >
        <Explain title="ki ksa diyalog" lead="Sorma + tarif etme birlikte">
          <Examples
            items={[
              {
                en: "A: Entschuldigung, wo ist die Bibliothek?  B: Geradeaus und die zweite Strae links.",
                tr: "A: Afedersiniz, ktphane nerede?  B: Dz gidin ve ikinci sokaktan sola.",
                pron: "ent-uldigung, vo ist di bibliyotek  gara-deaus und di tsvayte trase links",
              },
              {
                en: "A: Wie komme ich zum Bahnhof?  B: Nehmen Sie den Bus 140 bis zur Haltestelle Markt.",
                tr: "A: Gara nasl giderim?  B: 140 numaral otobse binin, Markt duranda inin.",
                pron: "vi kome ih tsum banhof  nemen zi den bus ains-fiatsiH bis tsua haltetele markt",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="had-section had-section--cta"
        data-toc-title="Mini-Test balants"
      >
        <Callout type="tip" title="Anladn m?">
          <p>Yol tarifleri ve prepozisyonlar iin mini teste ge:</p>
          <p>
            <Link className="quiz-link btn" to="../quiz">
              Mini-Teste git
            </Link>
          </p>
        </Callout>
      </section>
    </div>
  );
}
