import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function MixedConditionals() {
  return (
    <>
      <div className="rule-box">
        <strong>Mixed Conditionals</strong>: Koul (if-clause) ile sonu (main
        clause) farkl zaman dzlemlerinde. En yaygn iki tip:{" "}
        <Key>Past  Present</Key> ve <Key>Present  Past</Key>.
      </div>

      <Sub title="Past  Present (gemi koulun bugnk sonucu)" />
      <Formula
        rows={[
          ["Yap", "If + Past Perfect", "..., would + V1 (imdi/bugn sonu)"],
          ["rnek", "If I had studied hard,", "I would have a better job now."],
        ]}
      />
      <Callout type="info" title="Anlam">
        Gemite farkl bir ey olsayd, <em>bugn</em> durum farkl olurdu.
        Trkede eer o zaman yapsaydm, imdi  olurdu.
      </Callout>

      <Sub title="Present  Past (imdiki durumun gemiteki sonucu)" />
      <Formula
        rows={[
          [
            "Yap",
            "If + Past Simple (gerek-d imdi)",
            "..., would have + V3 (gemi sonu)",
          ],
          [
            "rnek",
            "If I were taller,",
            "I would have joined the team last year.",
          ],
        ]}
      />
      <Callout type="tip" title="were tercihi">
        <Key>If I were you</Key> gibi kalplarda B2de <strong>were</strong>{" "}
        yaygndr (resmi-yazl). Konumada <em>was</em> de duyulur.
      </Callout>

      <Sub title="Alternatifler: if it werent for / but for / Had + S + V3" />
      <MiniTable
        head={["Desen", "Anlam", "rnek"]}
        rows={[
          [
            "If it werent for + isim",
            " olmasa",
            "If it werent for your help, I wouldnt be here.",
          ],
          [
            "But for + isim",
            " olmasayd",
            "But for his advice, I would have failed.",
          ],
          [
            "Had + S + V3, ...",
            "If + Past Perfect (ksa)",
            "Had I known, I would have called.",
          ],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog" />
      <Examples
        items={[
          {
            en: "If she had taken the earlier train, she would be here by now.",
            tr: "Daha erken treni binseydi, imdi burada olurdu.",
            pron: "if ii hed teykn di rlir treyn, ii vud bi hir bay nau",
          },
          {
            en: "If I knew Spanish, I would have moved to Madrid last year.",
            tr: "spanyolca bilseydim, geen yl Madride tanm olurdum.",
            pron: "if ay nyuu speni, ay vud hev muvd t meadrid lest yir",
          },
          {
            en: "Had they informed us, we wouldnt have made that mistake.",
            tr: "Bizi bilgilendirmi olsaydlar, o hatay yapmazdk.",
            pron: "hed dey informd as, vii vudnt hev meyd det misteyk",
          },
          {
            en: "If it werent for coffee, I wouldnt be functional in the morning.",
            tr: "Kahve olmasa, sabahlar alr halde olmazdm.",
            pron: "if it vrnt for kafi, ay vudnt bi fanknl in d morni",
          },
        ]}
      />
      <Callout type="warn" title="Sk hatalar">
        <ul className="compact-list">
          <li>
            <strong>Kark zamanlar</strong>:{" "}
            <em> If I had money, I would have bought it now</em> {" "}
            <strong>imdi</strong> sonu iin <Key>would + V1</Key>.
          </li>
          <li>
            <strong>ift would</strong>: Koul yan tmcesinde <Key>would</Key>{" "}
            kullanlmaz: <em> If I would have studied</em>  <strong></strong>{" "}
            <Key>If I had studied</Key>.
          </li>
        </ul>
      </Callout>

      <Sub title="Altrma  Gap-fill" />
      <ExerciseFill
        title="Kark Koullar"
        items={[
          {
            id: "mx1",
            before: "If I ___ (listen) to you then, I ___ (be) happier now.",
            after: "",
            answers: ["had listened, would be"],
          },
          {
            id: "mx2",
            before:
              "If she ___ (be) more confident, she ___ (have + get) the job last month.",
            after: "",
            answers: [
              "were, would have got",
              "was, would have got",
              "were, would have gotten",
            ],
          },
          {
            id: "mx3",
            before: "Had they ___ (tell) me, I ___ (not / make) that mistake.",
            after: "",
            answers: ["told, wouldnt have made", "told, would not have made"],
          },
          {
            id: "mx4",
            before: "If it ___ (not / be) for your support, we ___ (fail).",
            after: "",
            answers: [
              "werent, would have failed",
              "hadnt been, would have failed",
            ],
          },
          {
            id: "mx5",
            before:
              "If I ___ (know) the rules, I ___ (not / break) them yesterday.",
            after: "",
            answers: ["knew, wouldnt have broken"],
          },
          {
            id: "mx6",
            before: "But for the rain, the concert ___ (go) ahead.",
            after: "",
            answers: ["would have gone"],
          },
        ]}
      />

      <ExerciseMC
        title="Mini Test"
        items={[
          {
            q: "Gemi koul  bugnk sonu hangi seenektir?",
            choices: [
              "If + Past Simple  would have + V3",
              "If + Past Perfect  would + V1",
              "If + Present Simple  will + V1",
            ],
            correctIndex: 1,
            explain:
              "Past Perfect + would + V1 = gemi koulun bugnk sonucu.",
          },
          {
            q: "Doru ksaltma hangisi?",
            choices: [
              "Had I know, ...",
              "Had I known, ...",
              "If I would know, ...",
            ],
            correctIndex: 1,
            explain: "Had + V3 ters evrilir: Had I known, ...",
          },
        ]}
      />
    </>
  );
}
