import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function Wechselpreps() {
  return (
    <>
      <div className="rule-box">
        <strong>Yn m, yoksa konum mu? (Wechselprpositionen)</strong>:
        <Key>in, an, auf, unter, ber, vor, hinter, neben, zwischen</Key>. Eer
        cmlede <strong>hareket</strong> ve <strong>var</strong> varsa 
        <Term>nereye?</Term> sor ve
        <Key>Akkusativ</Key> kullan. Sadece <strong>konum</strong> anlatlyorsa
        <Term>nerede?</Term> sor ve
        <Key>Dativ</Key> kullan.
      </div>

      <Explain
        title="Balang: Hedefe hareket mi var, yoksa sadece bulunduu yer mi? (Erst denken, dann whlen)"
        lead="Her cmlede nce sahneyi hayal et: Nesne/kii yer deitiriyor mu? Var noktas var m? Evetse Akkusativ. Sadece bulunduu yeri sylyorsan Dativ."
      >
        <p>
          Bu edatlarla iki farkl bilgi verirsin. <strong>Hareket/var</strong>{" "}
          varsa <Term>nereye?</Term> sorusunun cevab gelir ve edat{" "}
          <Key>Akkusativ</Key> alr:
          <em>
            {" "}
            Ich gehe <u>in die</u> Kche.
          </em>{" "}
          (nereye? mutfaa).
          <br />
          <strong>Konum</strong> varsa <Term>nerede?</Term> sorusunun cevab
          gelir ve edat <Key>Dativ</Key> alr:
          <em>
            {" "}
            Ich bin <u>in der</u> Kche.
          </em>{" "}
          (nerede? mutfakta).
        </p>
        <p>
          Bir ipucu: Cmlede <Term>koymak, yerletirmek, gtrmek, girmek</Term>{" "}
          gibi hareket fiilleri grrsen ou zaman Akkusativ gelir.{" "}
          <Term>durmak, olmak, bulunmak, asl durmak</Term> gibi fiiller konum
          anlatr ve Dativ ister.
        </p>
      </Explain>

      <Sub title="Adm adm seim (nereye?  Akkusativ, nerede?  Dativ)" />
      <Explain title="Uygulama plan" lead="Her cmlede u  adm uygula.">
        <ol className="compact-list">
          <li>
            Fiili tan: <strong>hareket mi</strong> (yer deiimi var) yoksa{" "}
            <strong>konum mu</strong> (yer deiimi yok)?
          </li>
          <li>
            Doru soruyu sor: <strong>nereye?</strong> ya da{" "}
            <strong>nerede?</strong>
          </li>
          <li>
            Edat ve hli se: nereye  <Key>Akkusativ</Key>, nerede {" "}
            <Key>Dativ</Key>.
          </li>
        </ol>
        <Formula
          rows={[
            [
              "Hareket/var (nereye?)",
              " + edat + artikel (Akkusativ) + yer",
              "Er stellt die Tasse <u>auf den</u> Tisch.",
            ],
            [
              "Konum (nerede?)",
              " + edat + artikel (Dativ) + yer",
              "Die Tasse steht <u>auf dem</u> Tisch.",
            ],
          ]}
        />
        <Callout type="tip" title="Mini kontrol listesi">
          <ul className="compact-list">
            <li>Var noktas var m?  Akkusativ.</li>
            <li>Yer deitirme yok, sadece bulunduu yer mi?  Dativ.</li>
            <li>
              Koymakdurmak iftini dene: <Term>stellen/stehen</Term>,{" "}
              <Term>legen/liegen</Term>, <Term>setzen/sitzen</Term>,{" "}
              <Term>hngen/hngen</Term>.
            </li>
          </ul>
        </Callout>
      </Explain>

      <Sub title="Edatlar anlamla ren (Trke aklama + Almanca rnek)" />

      <Explain
        title="in  iine / iinde"
        lead="Kapal bir alann snrlar vardr. ine hareket edebilir veya iinde bulunabilirsin."
      >
        <p>
          <Term>in</Term> kapal bir alana <strong>giri</strong> (Akkusativ) ya
          da o alann <strong>iinde bulunma</strong> (Dativ) bildirir. Hareket:{" "}
          <em>
            Ich gehe <u>ins</u> Zimmer.
          </em>{" "}
          Konum:{" "}
          <em>
            Ich bin <u>im</u> Zimmer.
          </em>
        </p>
        <Examples
          items={[
            {
              en: "Ich gehe ins Kino.",
              tr: "Sinemaya gidiyorum.",
              pron: "ih ge-e ins kino",
            },
            {
              en: "Ich bin im Kino.",
              tr: "Sinemadaym.",
              pron: "ih bin im kino",
            },
          ]}
        />
      </Explain>

      <Explain
        title="an  kenarna/yanna (dikey yzey, su kenar)  kenarnda/yannda"
        lead="Dikey yzeye temas veya su/ereve/pencere gibi bir kenara yaklamabulunma ilikisi."
      >
        <p>
          <Term>an</Term>, bir eyin <strong>kenarna/yanna hareket</strong>{" "}
          (Akkusativ) ya da <strong>kenarnda/yannda konum</strong> (Dativ)
          demektir. Hareket:{" "}
          <em>
            Ich hnge das Poster <u>an die</u> Wand.
          </em>{" "}
          Konum:{" "}
          <em>
            Das Poster hngt <u>an der</u> Wand.
          </em>
        </p>
        <Examples
          items={[
            {
              en: "Wir fahren ans Meer.",
              tr: "Denize gidiyoruz.",
              pron: "vir farn ans meer",
            },
            {
              en: "Wir sind am Meer.",
              tr: "Deniz kenarndayz.",
              pron: "vir zint am meer",
            },
          ]}
        />
      </Explain>

      <Explain
        title="auf  stne (yatay yzey)  stnde"
        lead="Masa, raf, tezgh gibi yatay yzey ilikisi."
      >
        <p>
          <Term>auf</Term> <strong>yatay yzey</strong>e doru hareket
          (Akkusativ) veya o yzeyin <strong>stnde konum</strong> (Dativ)
          demektir. Hareket:{" "}
          <em>
            Ich lege das Buch <u>auf den</u> Tisch.
          </em>{" "}
          Konum:{" "}
          <em>
            Das Buch liegt <u>auf dem</u> Tisch.
          </em>
        </p>
        <Examples
          items={[
            {
              en: "Stell die Tasse auf den Tisch.",
              tr: "Fincan masann stne koy.",
              pron: "tel di tase auf den ti",
            },
            {
              en: "Die Tasse steht auf dem Tisch.",
              tr: "Fincan masann stnde.",
              pron: "di tase teyt auf dem ti",
            },
          ]}
        />
      </Explain>

      <Explain
        title="unter  altna  altnda"
        lead="Bir eyin altna hareket etmek ya da aada bulunmak."
      >
        <p>
          <Term>unter</Term> hareket:{" "}
          <em>
            Die Katze luft <u>unter den</u> Tisch.
          </em>{" "}
          Konum:{" "}
          <em>
            Die Katze ist <u>unter dem</u> Tisch.
          </em>
        </p>
      </Explain>

      <Explain
        title="ber  zerine/stnden (temassz)  zerinde (temassz)"
        lead="Yukardan, temas olmadan kurulan iliki; umak, asl durmak gibi."
      >
        <p>
          <Term>ber</Term> genelde <strong>temas yok</strong>. Hareket:{" "}
          <em>
            Der Vogel fliegt <u>ber den</u> See.
          </em>{" "}
          Konum:{" "}
          <em>
            Die Lampe hngt <u>ber dem</u> Tisch.
          </em>
        </p>
      </Explain>

      <Explain
        title="vor / hinter  nne/arkasna  nnde/arkasnda"
        lead="narka ekseninde hareket ya da konum."
      >
        <p>
          <Term>vor</Term> ve <Term>hinter</Term> ile hareket:{" "}
          <em>
            Ich stelle das Auto <u>vor das</u> Haus.
          </em>{" "}
          Konum:{" "}
          <em>
            Das Auto steht <u>vor dem</u> Haus.
          </em>
          <br />
          Arkas iin:{" "}
          <em>
            Er parkt <u>hinter das</u> Haus.
          </em>{" "}
          /{" "}
          <em>
            Das Auto steht <u>hinter dem</u> Haus.
          </em>
        </p>
      </Explain>

      <Explain
        title="neben / zwischen  yanna/arasna  yannda/arasnda"
        lead="Yankomuluk veya iki nesnenin arasna/ arasnda bulunma."
      >
        <p>
          <Term>neben</Term> hareket:{" "}
          <em>
            Ich setze mich <u>neben den</u> Mann.
          </em>{" "}
          Konum:{" "}
          <em>
            Ich sitze <u>neben dem</u> Mann.
          </em>
          <br />
          <Term>zwischen</Term> hareket:{" "}
          <em>
            Ich stelle den Stuhl <u>zwischen die</u> Tische.
          </em>{" "}
          Konum:{" "}
          <em>
            Der Stuhl steht <u>zwischen den</u> Tischen.
          </em>
        </p>
      </Explain>

      <Sub title="Koymak ve durmak fiil iftleriyle gvenli seim" />
      <Explain
        title="Neden bu iftler ok faydal?"
        lead="Ayn sahneyi iki ekilde anlat: nce hareket fiiliyle yerletir (Akkusativ), sonra konum fiiliyle nerede durduunu syle (Dativ)."
      >
        <p>
          Hareket fiilleri ounlukla <strong>Akkusativ</strong> ister:{" "}
          <Term>legen</Term> (yatay koymak), <Term>stellen</Term> (dikey
          koymak),
          <Term>setzen</Term> (oturtmak), <Term>hngen</Term> (asmak). Konum
          fiilleri ounlukla <strong>Dativ</strong> ister:
          <Term>liegen</Term> (yatay durmak), <Term>stehen</Term> (ayakta
          durmak), <Term>sitzen</Term> (oturmak),
          <Term>hngen</Term> (asl durmak).
        </p>
      </Explain>
      <MiniTable
        head={["Hareket (Akkusativ)", "Konum (Dativ)", "rnek cmle ifti"]}
        rows={[
          [
            "legen",
            "liegen",
            "Ich lege das Handy <b>auf den</b> Tisch. / Das Handy liegt <b>auf dem</b> Tisch.",
          ],
          [
            "stellen",
            "stehen",
            "Ich stelle die Flasche <b>in den</b> Schrank. / Die Flasche steht <b>im</b> Schrank.",
          ],
          [
            "setzen",
            "sitzen",
            "Ich setze das Kind <b>auf den</b> Stuhl. / Das Kind sitzt <b>auf dem</b> Stuhl.",
          ],
          [
            "hngen",
            "hngen",
            "Ich hnge das Bild <b>an die</b> Wand. / Das Bild hngt <b>an der</b> Wand.",
          ],
        ]}
      />

      <Sub title="Ksaltmalar ve doal syleyi (ins, im, ans, am)" />
      <Explain
        title="Neden ksaltma var?"
        lead="Konumada akc olmak iin artikel + edat bazen birleerek tek bir biim olur."
      >
        <p>
          <code>in das  ins</code> (Ich gehe <u>ins</u> Kino),{" "}
          <code>in dem  im</code> (Ich bin <u>im</u> Kino),
          <code>an das  ans</code> (ans Fenster), <code>an dem  am</code> (am
          Fenster).
        </p>
      </Explain>

      <Sub title="Sk yaplan hatalar ve ipular" />
      <Callout type="error" title="NereyeNerede karkl">
        <ul className="compact-list">
          <li>
            <em>*Ich gehe in dem Park.</em> yerine{" "}
            <Term>
              Ich gehe <u>in den</u> Park.
            </Term>{" "}
            (hareket).
          </li>
          <li>
            <em>*Ich bin in den Park.</em> yerine{" "}
            <Term>
              Ich bin <u>im</u> Park.
            </Term>{" "}
            (konum).
          </li>
          <li>
            <Term>an</Term> ou zaman duvar/pencere/kenar; <Term>auf</Term>{" "}
            yatay yzey; <Term>ber</Term> temassz yukardadr.
          </li>
        </ul>
      </Callout>

      <Sub title="Telaffuz ipular (Trke odakl)" />
      <Explain
        title="Kulaa nasl gelir?"
        lead="Basit yaklam kullan; uzunksa nly Trke yazmyla yaklak ver."
      >
        <ul className="compact-list">
          <li>
            <Term>in</Term>  <em>in</em>, <Term>an</Term>  <em>an</em>.
          </li>
          <li>
            <Term>auf</Term>  <em>auf</em> (au  av).
          </li>
          <li>
            <Term>ber</Term>  <em>-ber</em>, <Term>unter</Term> {" "}
            <em>un-ter</em>.
          </li>
          <li>
            <Term>vor</Term>  <em>for</em>, <Term>hinter</Term> {" "}
            <em>hinter</em>, <Term>neben</Term>  <em>ne-ben</em>,{" "}
            <Term>zwischen</Term>  <em>ts-vi-n</em>.
          </li>
        </ul>
      </Explain>

      <Sub title="Mikro altrmalar: doru edat + artikel (seimli)" />
      <ExerciseMC
        title="nereye?  Akkusativ  nerede?  Dativ"
        items={[
          {
            q: "Ich gehe ___ Park. (nereye?)",
            choices: ["im", "in den", "in dem"],
            correctIndex: 1,
            explain: "Hareket/var var  Akkusativ: in den Park.",
          },
          {
            q: "Ich bin ___ Park. (nerede?)",
            choices: ["ins", "im", "in den"],
            correctIndex: 1,
            explain: "Konum  Dativ: im Park (= in dem Park).",
          },
          {
            q: "Stell die Flasche ___ Tisch! (nereye?)",
            choices: ["auf dem", "auf den", "auf der"],
            correctIndex: 1,
            explain: "Hareket/var  Akkusativ: auf den Tisch.",
          },
          {
            q: "Die Flasche steht ___ Tisch. (nerede?)",
            choices: ["auf den", "auf dem", "auf der"],
            correctIndex: 1,
            explain: "Konum  Dativ: auf dem Tisch.",
          },
          {
            q: "Wir fahren ___ Meer. (nereye?)",
            choices: ["ans", "am", "an dem"],
            correctIndex: 0,
            explain: "Hareket/var  Akkusativ: ans Meer (= an das Meer).",
          },
          {
            q: "Wir sind ___ Meer. (nerede?)",
            choices: ["ans", "am", "an das"],
            correctIndex: 1,
            explain: "Konum  Dativ: am Meer (= an dem Meer).",
          },
          {
            q: "Ich setze mich ___ Stuhl. (nereye?)",
            choices: ["auf den", "auf dem", "auf der"],
            correctIndex: 0,
            explain: "Hareket/var  Akkusativ: auf den Stuhl.",
          },
          {
            q: "Ich sitze ___ Stuhl. (nerede?)",
            choices: ["auf den", "auf dem", "auf der"],
            correctIndex: 1,
            explain: "Konum  Dativ: auf dem Stuhl.",
          },
        ]}
      />

      <Sub title="Mini Test (oktan semeli)" />
      <ExerciseMC
        title="Wechselprpositionen  kontrol"
        items={[
          {
            q: "Doru olan hangisi? Sinemaya gidiyorum.",
            choices: [
              "Ich gehe im Kino.",
              "Ich gehe ins Kino.",
              "Ich bin ins Kino.",
            ],
            correctIndex: 1,
            explain: "Hareket/var var: nereye?  Akkusativ: ins Kino.",
          },
          {
            q: "Doru olan hangisi? Poster duvarda asl.",
            choices: [
              "Das Poster hngt an der Wand.",
              "Das Poster hngt an die Wand.",
              "Das Poster hngt auf der Wand.",
            ],
            correctIndex: 0,
            explain: "Konum anlatlyor: nerede?  Dativ: an der Wand.",
          },
          {
            q: "Hangi cmlede yatay yzey ilikisi var?",
            choices: [
              "Das Bild hngt an der Wand.",
              "Die Tasse steht auf dem Tisch.",
              "Wir sind am Meer.",
            ],
            correctIndex: 1,
            explain: "Masa yatay yzeydir: auf dem Tisch.",
          },
          {
            q: "Arabay evin nne koydum. cmlesi hangisi?",
            choices: [
              "Ich stelle das Auto vor dem Haus.",
              "Ich stelle das Auto vor das Haus.",
              "Das Auto steht vor dem Haus.",
            ],
            correctIndex: 1,
            explain:
              "Hareket/var: nereye?  vor das Haus. Konum cmlesi: steht vor dem Haus.",
          },
        ]}
      />
    </>
  );
}
