// src/pages/Grammar/sections/ger/a2/ParticlesDochMal.jsx
import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function ParticlesDochMal() {
  return (
    <>
      <div className="rule-box">
        <strong>Partikeller: doch, mal (Modalpartikeln)</strong>: Bu iki kk
        kelime cmlenin <Key>tonunu</Key> ayarlar: rica eder, tevik eder,
        yumuatr ya da aknlk/itiraz katar. Tam bir Trke karl yok;
        balama gre
        <em> hadi, ya, bir kez, baksana, ama canm</em> gibi duygular verir.
      </div>

      <Explain
        title="Neden doch ve mal renmeliyiz? (Warum doch/mal?)"
        lead="Ayn cmleyi daha doal, daha nazik veya daha motive edici sylemek iin."
      >
        <p>
          Almancada <Term>modalpartikeller</Term> denen bu kk kelimeler,
          cmlenin
          <strong>duygusunu</strong> ve <strong>niyetini</strong> gsterir.
          Gramer anlam tamazlar; <strong>ton</strong> verirler. Trkedeki{" "}
          <em>hadi, -ya, bir, baksana</em> gibi eklemeler gibidir. zellikle{" "}
          <Term>rica</Term>, <Term>tevik</Term>, <Term>nazik emir</Term> ve{" "}
          <Term>itiraz</Term> etmekte ok kullanldrlar.
        </p>
        <p>
          Basit kural: <Key>Emirde</Key> genellikle fiilden hemen sonra
          gelirler:{" "}
          <em>
            Komm <u>doch</u>!
          </em>{" "}
          /{" "}
          <em>
            Mach <u>mal</u> auf!
          </em>{" "}
          Normal cmlelerde de <strong>ekimli fiilden sonra</strong>, orta
          alanda yer alr:{" "}
          <em>
            Das ist <u>doch</u> klar.
          </em>
        </p>
      </Explain>

      <Sub title="doch (tevik, itiraz, hatrlatma) (doch)" />
      <Explain
        title="Ne zaman doch?"
        lead="doch ile konumaya duygu katarsn: ama bak, hadi ya, aslnda yle."
      >
        <ul className="compact-list">
          <li>
            <strong>Tevik / davet</strong>: Yumuaka ikna etme.{" "}
            <em>
              Komm <u>doch</u> mit!
            </em>{" "}
            (Hadi gel, ya.)
          </li>
          <li>
            <strong>tiraz / dzeltme</strong>: Kardakinin dediine nazike
            kar kma.{" "}
            <em>
              Das stimmt <u>doch</u> nicht.
            </em>{" "}
            (Ama bu doru deil ki.)
          </li>
          <li>
            <strong>Hatrlatma / bariz olan vurgulama</strong>:{" "}
            <em>
              Du weit das <u>doch</u>.
            </em>{" "}
            (Bunu zaten biliyorsun ya.)
          </li>
          <li>
            <strong>aknlk / sitem</strong>:{" "}
            <em>
              Warum sagst du <u>doch</u> nichts?
            </em>{" "}
            (Neden ama hibir ey sylemiyorsun ki?)
          </li>
        </ul>
        <Callout type="info" title="Ton nemlidir">
          <p>
            <Term>doch</Term> bazen yumuatr (<em>Komm doch!</em>), bazen de
            hafif itiraz eder (<em>Das ist doch klar.</em>). Ses tonun ve balam
            anlam belirler.
          </p>
        </Callout>
      </Explain>

      <Examples
        items={[
          {
            en: "Komm doch mit!",
            tr: "Hadi gel (ya).",
            pron: "kom dohH mit",
          },
          {
            en: "Das ist doch klar.",
            tr: "Bu zaten ak (ama canm, belli yani).",
            pron: "das ist dohH klar",
          },
          {
            en: "Das stimmt doch nicht.",
            tr: "Ama bu doru deil ki.",
            pron: "das timt dohH niHt",
          },
        ]}
      />

      <Sub title="mal (yumuatma, nazik rica, bir kere/bi) (mal)" />
      <Explain
        title="Ne zaman mal?"
        lead="Emirleri yumuatr, gnlk konumay doal yapar: bir bak, bi gelsene."
      >
        <ul className="compact-list">
          <li>
            <strong>Nazik rica / yumuatma</strong>:{" "}
            <em>
              Mach <u>mal</u> das Fenster auf.
            </em>{" "}
            (u pencereyi bi asana.)
          </li>
          <li>
            <strong>Ksa bir eylem / yle bir</strong>:{" "}
            <em>
              Warte <u>mal</u> kurz.
            </em>{" "}
            (Bi saniye bekle.)
          </li>
          <li>
            <strong>Soruda doal ton</strong>:{" "}
            <em>
              Kannst du <u>mal</u> helfen?
            </em>{" "}
            (Bi yardm edebilir misin?)
          </li>
        </ul>
        <Callout type="tip" title="Naziklik fark">
          <p>
            <em>Mach die Tr auf!</em> sert olabilir.{" "}
            <em>
              Mach <u>mal</u> die Tr auf.
            </em>{" "}
            daha <strong>nazik</strong> ve <strong>doal</strong> gelir.
          </p>
        </Callout>
      </Explain>

      <Examples
        items={[
          {
            en: "Warte mal kurz.",
            tr: "Bi saniye bekle.",
            pron: "varte mal kurz",
          },
          {
            en: "Schau mal!",
            tr: "Bi bak!",
            pron: "au mal",
          },
          {
            en: "Kannst du mal kommen?",
            tr: "Bi gelebilir misin?",
            pron: "kanst du mal kommen",
          },
        ]}
      />

      <Sub title="doch mal birlikte (nazik tevik) (doch mal  zusammen)" />
      <Explain
        title="Neden doch mal birlikte?"
        lead="Hadi bi dene gibi hem tevik eder hem nazikletirir."
      >
        <p>
          <Term>doch mal</Term> ikilisi ok yaygndr:{" "}
          <em>
            Probier <u>doch mal</u>!
          </em>{" "}
          (Hadi bir dene!) Bu yap nazik, arkadaa ve motive edicidir.{" "}
          <em>
            Komm <u>doch mal</u> her!
          </em>{" "}
          (Hadi bir gel buraya.)
        </p>
      </Explain>

      <Examples
        items={[
          {
            en: "Probier doch mal!",
            tr: "Hadi bir dene!",
            pron: "probir dohH mal",
          },
          {
            en: "Ruf ihn doch mal an.",
            tr: "Hadi onu bir ara.",
            pron: "ruf in dohH mal an",
          },
        ]}
      />

      <Sub title="Ksa zet tablo (Schnellberblick)" />
      <MiniTable
        head={["Parack", "Ana ilev", "rnek"]}
        rows={[
          [
            "doch",
            "tevik / itiraz / barizlii vurgulama",
            "Komm doch mit!  Das ist doch klar.",
          ],
          ["mal", "nazik rica / yumuatma", "Warte mal kurz.  Mach mal auf."],
          [
            "doch mal",
            "nazik tevik (hadi bir)",
            "Probier doch mal!  Ruf doch mal an.",
          ],
        ]}
      />

      <Sub title="Dikkat ve kullanm ipular (Hinweise)" />
      <Callout type="warn" title="Fazla kullanma, balama bak">
        <ul className="compact-list">
          <li>
            Resm e-postalarda <Term>mal</Term> yerine daha ntr ifadeler
            kullan:
            <em> Knnten Sie bitte ?</em>
          </li>
          <li>
            <Term>doch</Term> yanl tonla sert duyulabilir. Nazik syle:{" "}
            <em>Komm doch mal bitte kurz.</em>
          </li>
        </ul>
      </Callout>

      <Sub title="Boluk doldurma (doru parac se ve yaz)" />
      <ExerciseFill
        title="doch / mal / doch mal /  (gerek yok)"
        items={[
          {
            id: "pm1",
            before: "_____ komm mit! (hadi gel, tevik)",
            after: "",
            answers: ["doch"],
          },
          {
            id: "pm2",
            before: "Mach _____ die Tr auf. (nazik rica, yumuatma)",
            after: "",
            answers: ["mal"],
          },
          {
            id: "pm3",
            before: "Probier _____! (hadi bir dene)",
            after: "",
            answers: ["doch mal", "mal doch"],
          },
          {
            id: "pm4",
            before: "Das stimmt _____ nicht. (nazik itiraz)",
            after: "",
            answers: ["doch"],
          },
          {
            id: "pm5",
            before: "Warte _____ kurz! (ksa bekleme, doal ton)",
            after: "",
            answers: ["mal"],
          },
          {
            id: "pm6",
            before: "Ruf sie _____ an. (nazik tevik: bir ara)",
            after: "",
            answers: ["doch mal"],
          },
        ]}
      />

      <Sub title="Mini Test (3 seenekli eldiricilerle)" />
      <ExerciseMC
        title="doch / mal  doru kullanm"
        items={[
          {
            q: "Hangi cmle nazik ricadr?",
            choices: [
              "Mach die Tr auf!",
              "Mach mal die Tr auf.",
              "Die Tr, mach auf.",
            ],
            correctIndex: 1,
            explain: "mal emir tonunu yumuatr: Mach mal die Tr auf.",
          },
          {
            q: "Hangi cmle nazik teviktir?",
            choices: ["Probier doch mal!", "Probier!", "Du probierst."],
            correctIndex: 0,
            explain: "doch mal birlikte motive eder: Hadi bir dene!",
          },
          {
            q: "Hangi cmle itiraz anlam tar?",
            choices: [
              "Das ist mal klar.",
              "Das ist doch klar.",
              "Das ist klar mal.",
            ],
            correctIndex: 1,
            explain:
              "doch burada zaten anlaml vurgudur: Bariz olan hatrlatr.",
          },
          {
            q: "Hangisi gnlk ve nazik bir ar?",
            choices: ["Komm doch mal her!", "Komm her!", "Komm her mal doch!"],
            correctIndex: 0,
            explain: "doch mal yakn ve nazik bir davet tonu verir.",
          },
          {
            q: "Hangi seenek doal deildir?",
            choices: [
              "Kannst du mal helfen?",
              "Komm doch mit!",
              "Das mal stimmt nicht.",
            ],
            correctIndex: 2,
            explain:
              "mal bu konumda doal deil; Das stimmt doch nicht. doru olur.",
          },
        ]}
      />
    </>
  );
}
