import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

const FAMILY_CORE = [
  ["die Mutter", "anne", "mutta"],
  ["der Vater", "baba", "fata"],
  ["die Eltern (Pl.)", "ebeveynler", "eltn"],
  ["die Tochter", "kz evlat", "toHta"],
  ["der Sohn", "erkek evlat", "zon"],
  ["die Schwester", "kz karde", "vesta"],
  ["der Bruder", "erkek karde", "bruda"],
  ["die Frau", "e (kadn) / kadn", "frau"],
  ["der Mann", "e (erkek) / adam", "man"],
  ["die Gromutter (Oma)", "byk anne", "gross-mutta / oma"],
  ["der Grovater (Opa)", "byk baba", "gross-fata / opa"],
  ["die Tante", "teyze/hala", "tante"],
  ["der Onkel", "amca/day", "onkel"],
  ["die Cousine", "kuzen (kz)", "kuziine"],
  ["der Cousin", "kuzen (erkek)", "kuzeng (Fr. okunu)"],
];

export default function Family() {
  return (
    <div className="ffh-sub ffh-sub--family" data-sub="family">
      <section
        className="ffh-section ffh-section--terms"
        data-toc-title="Aile szl"
      >
        <Explain
          title="Temel aile szckleri"
          lead="nce yakn akrabalklar. Yazm + okunu  hzl ezber."
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={FAMILY_CORE}
          />
          <Callout type="info" title="oullar">
            <p>
              <Term>der Bruder  die Brder</Term>,{" "}
              <Term>die Schwester  die Schwestern</Term>,
              <Term>das Kind  die Kinder</Term>. A1de tablolarla greceiz;
              imdilik kalp olarak ren.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--poss"
        data-toc-title="mein/meine kullanm"
      >
        <Explain
          title="yelik: mein / meine"
          lead="Trkede ek (-im), Almancada ayr szck"
        >
          <Formula
            rows={[
              [
                "Eril/Ntr isim",
                "mein Vater / mein Kind",
                "benim babam / benim ocuum",
              ],
              [
                "Diil/oul",
                "meine Mutter / meine Eltern",
                "benim annem / ebeveynlerim",
              ],
              ["Senin", "dein/deine", "dein Bruder / deine Schwester"],
              [
                "Onun (erkek/kadn)",
                "sein/ihr",
                "sein Vater / ihr Vater",
              ],
            ]}
          />
          <Callout type="warning" title="Hata uyars">
            <p>
              <Key>*meine Vater</Key> deil  <strong>mein Vater</strong>{" "}
              (eril). Diillerde <strong>meine</strong>:<Key>meine Mutter</Key>.
            </p>
          </Callout>
          <Examples
            items={[
              {
                en: "Das ist mein Bruder. Er heit Ali.",
                tr: "Bu benim erkek kardeim. Ad Ali.",
                pron: "das ist mayn bruda. ea hays(t) ali",
              },
              {
                en: "Meine Schwester wohnt in Ankara.",
                tr: "Kz kardeim Ankarada yayor.",
                pron: "mayn vesta vont in ankra",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--have"
        data-toc-title="Ich habe  kalb"
      >
        <Explain title="Sahiplik: Ich habe " lead="Basit ve gl bir kalp">
          <Formula
            rows={[
              [
                "Var/yok",
                "Ich habe einen Bruder / Ich habe keine Schwester.",
                "Bir erkek kardeim var / Kz kardeim yok.",
              ],
            ]}
          />
          <Callout type="tip" title="Artikel ipucu (Akkusativ)">
            <p>
              <Term>einen Bruder</Term> (eril, Akkusativ) {" "}
              <Term>eine Schwester</Term> (diil). Ayrnts <em>Akkusativ</em>{" "}
              blmnde; A1 iin bu kalplar ezberle.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialogs"
        data-toc-title="Diyaloglar"
      >
        <Explain title="Ksa diyaloglar" lead="Soru-cevap modelle">
          <Examples
            items={[
              {
                en: "Hast du Geschwister?  Ja, ich habe eine Schwester.",
                tr: "Kardein var m?  Evet, bir kz kardeim var.",
                pron: "hast du geviste  ya, ih habe ayn vesta",
              },
              {
                en: "Wie alt ist dein Bruder?  Er ist 16.",
                tr: "Kardein ka yanda?  16.",
                pron: "vi alt ist dayn bruda  ea ist zeks-tsen",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
