import React from "react";
import { Link } from "react-router-dom";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Time() {
  return (
    <div className="ntd-sub ntd-sub--time" data-sub="time">
      <section
        className="ntd-section ntd-section--ask"
        data-toc-title="Saat sorma"
      >
        <Explain title="Saat sorma" lead="ki kalp yaygn ve edeer:">
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              ["Wie spt ist es?", "Saat ka?", "vi peet ist es"],
              ["Wie viel Uhr ist es?", "Saat ka?", "vi fiil uua ist es"],
            ]}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--say"
        data-toc-title="Saat syleme"
      >
        <Explain
          title="Saat syleme: tam saat"
          lead="<Term>Es ist</Term> + say + <Term>Uhr</Term>"
        >
          <MiniTable
            head={["Saat", "Almanca", "Okunu (TR)"]}
            rows={[
              ["08:00", "Es ist acht Uhr.", "es ist aHt uua"],
              ["14:00", "Es ist vierzehn Uhr.", "es ist fiirtsen uua"],
              ["20:00", "Es ist zwanzig Uhr.", "es ist tsvansih uua"],
            ]}
          />
        </Explain>

        <Explain
          title="Yarm & eyrek  byk fark!"
          lead="Almancada <Key>halb</Key> sonraki saate yarm kala demektir."
        >
          <MiniTable
            head={["Saat", "Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "08:30",
                "Es ist halb neun.",
                "Sekiz buuk.",
                "es ist halp noyn",
              ],
              [
                "07:15",
                "Viertel nach sieben.",
                "Yediyi eyrek gee.",
                "firtl nah ziibn",
              ],
              [
                "07:45",
                "Viertel vor acht.",
                "Sekize eyrek kala.",
                "firtl foa aHt",
              ],
              ["08:05", "fnf nach acht.", "Sekizi be gee.", "fnf nah aHt"],
              ["07:55", "fnf vor acht.", "Sekize be kala.", "fnf foa aHt"],
            ]}
          />
          <Callout type="warning" title="Karklk uyars">
            <p>
              <Key>halb neun</Key> = 08:30. Trkedeki dokuz buuk = 21:30
              deil, 9:30dur. Almancada yarm ifadesi{" "}
              <strong>bir sonraki saat</strong> baz alnarak sylenir.
            </p>
          </Callout>
        </Explain>

        <Explain
          title="Zaman edatlar (randevu/plan)"
          lead="A1 iin ilevsel kk kelimeler"
        >
          <MiniTable
            head={["Kalp", "Anlam", "rnek"]}
            rows={[
              ["um + Saat", "da", "um 9 Uhr (saat 9da)"],
              ["von  bis ", "dan a kadar", "von 8 bis 12 Uhr"],
              ["ab", "itibaren", "ab morgen / ab 10 Uhr"],
              ["seit", "den beri", "seit 2019; seit zwei Stunden"],
              ["gegen", "civarnda", "gegen 18 Uhr (18:00 gibi)"],
              ["am Morgen/Abend", "sabah/akam", "am Abend lerne ich Deutsch."],
            ]}
          />
        </Explain>

        <Explain title="Ksa diyaloglar" lead="Doal kullanm gr">
          <Examples
            items={[
              {
                en: "A: Wann treffen wir uns?  B: Um halb acht.",
                tr: "A: Ne zaman buluuyoruz?  B: Yedi buukta.",
                pron: "van treffen viA uns  um halp aHt",
              },
              {
                en: "A: Der Kurs ist von 9 bis 12 Uhr.",
                tr: "A: Kurs 9dan 12ye kadar.",
                pron: "dea kurs ist fon noyN bis zlf uua",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--cta"
        data-toc-title="Mini-Test balants"
      >
        <Callout type="tip" title="Hazr msn?">
          <p>Bu konunun mini testini ayr sayfada zmek iin tkla:</p>
          <p>
            <Link className="quiz-link btn" to="../quiz">
              Mini-Teste git
            </Link>
          </p>
        </Callout>
      </section>
    </div>
  );
}
