import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function ParticipleClauses() {
  return (
    <>
      <div className="rule-box">
        <strong>Participle Clauses</strong>: Cmleyi ksaltp akc hale
        getirir.
        <Key>Present (-ing)</Key>, <Key>Past (-ed/V3)</Key>,{" "}
        <Key>Perfect (having + V3)</Key>. Genellikle <strong>ayn zne</strong>{" "}
        kural vardr.
      </div>

      <Sub title="Present Participle (V-ing)  ayn anda / sebep / sonu" />
      <MiniTable
        head={["liki", "Yap", "rnek"]}
        rows={[
          ["Zaman/Ezamanl", "V-ing", "Walking down the street, I met Ali."],
          ["Sebep", "V-ing", "Feeling tired, she went to bed early."],
          ["Sonu", "V-ing", "He slipped, hitting his head."],
        ]}
      />
      <Callout type="tip" title="Ayn zne kural">
        <em>Walking down the street,</em> ksmnn znesi{" "}
        <strong>sonraki cmlenin znesiyle</strong> ayn olmal:
        <em> Walking down the street, the rain started.</em> (yanl)
      </Callout>

      <Sub title="Past Participle (V3)  edilgen anlam / nce gelen eylem" />
      <Examples
        items={[
          {
            en: "Given more time, we could finish.",
            tr: "Daha ok zaman verilirse bitirebiliriz.",
            pron: "givn mor taym, vii kud fn",
          },
          {
            en: "Left unattended, the machine overheats.",
            tr: "Kontrolsz braklnca makine ar snr.",
            pron: "left anaten-dd, d maiin ouvr-hiits",
          },
        ]}
      />

      <Sub title="Perfect Participle (having + V3)  nce biten eylem" />
      <MiniTable
        head={["Yap", "rnek", "Anlam"]}
        rows={[
          [
            "Having + V3",
            "Having finished the report, she went home.",
            "Raporu bitirdikten sonra",
          ],
          [
            "Having been + V3",
            "Having been warned, he acted carefully.",
            "Uyarld iin",
          ],
        ]}
      />

      <Sub title="Relative clause ksaltma (reduction)" />
      <MiniTable
        head={["Tam biim", "Ksaltma", "Not"]}
        rows={[
          ["Students who live in Ankara", "Students living in Ankara", "Etken"],
          [
            "Books that were written in 19th c.",
            "Books written in the 19th c.",
            "Edilgen",
          ],
        ]}
      />

      <Sub title="rnekler" />
      <Examples
        items={[
          {
            en: "Not knowing what to say, I kept silent.",
            tr: "Ne diyeceimi bilmediim iin sustum.",
            pron: "not noui vot t sey, ay kept saylnt",
          },
          {
            en: "Shocked by the news, they called their parents.",
            tr: "Haber karsnda oka urayp ailelerini aradlar.",
            pron: "okd bay d nyuus, dey kold der pernts",
          },
          {
            en: "Having worked all day, he was exhausted.",
            tr: "Btn gn altndan bitkindi.",
            pron: "hevi vrkt ol dey, hii woz egzostd",
          },
        ]}
      />
      <Callout type="warn" title="Sk hatalar">
        <ul className="compact-list">
          <li>
            <strong>Dangling participle</strong>: Yan tmcenin znesi belli
            deilse anlam kayar.
          </li>
          <li>
            <strong>Ksa yazma taknts</strong>: Akademik yazda gzel; ama
            ar kullanm okunabilirlii drr.
          </li>
        </ul>
      </Callout>

      <Sub title="Altrma  Gap-fill" />
      <ExerciseFill
        title="Participle Practice"
        items={[
          {
            id: "pc1",
            before: "___ (Not / know) the answer, I asked the teacher.",
            after: "",
            answers: ["Not knowing"],
          },
          {
            id: "pc2",
            before: "___ (Warn) about the risks, she refused to sign.",
            after: "",
            answers: ["Warned", "Having been warned"],
          },
          {
            id: "pc3",
            before: "___ (Finish) the project, we took a day off.",
            after: "",
            answers: ["Having finished"],
          },
          {
            id: "pc4",
            before: "Students ___ (live) in dorms must register.",
            after: "",
            answers: ["living"],
          },
          {
            id: "pc5",
            before: "Books ___ (write) before 1900 are in the archive.",
            after: "",
            answers: ["written"],
          },
        ]}
      />

      <ExerciseMC
        title="Mini Test"
        items={[
          {
            q: "Hangi cmlede zne uyumu HATALI?",
            choices: [
              "Walking along the river, I saw a swan.",
              "Having finished, the report was uploaded.",
              "Shocked by the news, she cried.",
            ],
            correctIndex: 1,
            explain:
              "Having finished kimin bitirdii belirsiz; zne uyumsuz (dangling).",
          },
        ]}
      />
    </>
  );
}
