// src/pages/Grammar/sections/ger/a2/ReflexiveVerbs.jsx
import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function ReflexiveVerbs() {
  return (
    <>
      <div className="rule-box">
        <strong>Dnl Fiiller (Reflexive Verben)</strong>: znenin yapt
        eylem yine znenin kendisini etkiler. Temel yap:
        <Key>zne + refleksif zamir + fiil</Key>. rn.:
        <em>
          {" "}
          Ich <u>wasche mich</u>.
        </em>{" "}
        /
        <em>
          {" "}
          Wir <u>treffen uns</u>.
        </em>
      </div>

      <Explain
        title="Dnl fiil nedir? (Was sind Reflexivverben?)"
        lead="Eylemin sonucu znenin kendisine dner ya da eylem karlkldr."
      >
        <p>
          <strong>Dnl</strong> kullanmda kii, eylemi kendi zerinde yapar:
          <em> Ich wasche mich.</em> (Kendimi ykyorum.)
          <br />
          <strong>Karlkl</strong> kullanmda eylem iki kii arasnda olur:
          <em> Wir treffen uns.</em> (Birbirimizle buluuyoruz.)
        </p>
        <p>
          Bu fiiller kiiyle <Term>uyumlu refleksif zamir</Term> ister:
          <Term> mich, dich, sich, uns, euch, sich</Term>. Hangi zamir
          gerektiini aadaki tabloda greceksin.
        </p>
      </Explain>

      <Sub title="Refleksif zamirler  kii uyumu (Reflexivpronomen)" />
      <Explain
        title="Akkusativ ve Dativ fark nasl anlalr?"
        lead="ou dnl fiil Akkusativ ister. Beden paras gibi bir nesne varsa ou kez Dativ + artikel kullanlr."
      >
        <p>
          <strong>Akkusativ (i hli)</strong>: Eylem dorudan kendine ynelir.
          <em>
            {" "}
            Ich wasche <u>mich</u>.
          </em>
        </p>
        <p>
          <strong>Dativ (e/a hli)</strong>: Kendime anlam + nesne
          belirtirsin (beden paras genelde artikel ile gelir).
          <em>
            {" "}
            Ich wasche <u>mir</u> die Hnde.
          </em>{" "}
          (Ellerimi ykyorum.)
        </p>
      </Explain>
      <MiniTable
        head={["Kii", "Akkusativ (kendisini)", "Dativ (kendisine)"]}
        rows={[
          ["ich", "mich", "mir"],
          ["du", "dich", "dir"],
          ["er/sie/es", "sich", "sich"],
          ["wir", "uns", "uns"],
          ["ihr", "euch", "euch"],
          ["sie/Sie", "sich", "sich"],
        ]}
      />
      <Examples
        items={[
          {
            en: "Ich wasche mich.",
            tr: "Kendimi ykyorum.",
            pron: "ih va-e miH",
          },
          {
            en: "Ich wasche mir die Hnde.",
            tr: "Ellerimi ykyorum.",
            pron: "ih va-e mi:r di hende",
          },
          {
            en: "Wir treffen uns im Park.",
            tr: "Parkta buluuyoruz.",
            pron: "vi:r trefn uns im park",
          },
        ]}
      />

      <Sub title="Gnlk kalplar (Alltag)  anlam ve rnekleri" />
      <Explain
        title="Neden bu kalplar nemli?"
        lead="A2 seviyesinde gnlk rutini anlatrken en ok bu dnl fiilleri kullanrsn."
      >
        <p>
          Aadaki cmlelerde nce <strong>Trke aklama</strong>, sonra{" "}
          <strong>rnek</strong> ve <strong>yaklak okunu</strong> var.
        </p>
      </Explain>
      <MiniTable
        head={["Kalp", "Anlam (TR)", "rnek cmle"]}
        rows={[
          [
            "sich waschen",
            "kendini ykamak",
            "Ich wasche mich jeden Morgen. (Her sabah kendimi ykarm.)",
          ],
          [
            "sich anziehen",
            "giyinmek",
            "Ich ziehe mich schnell an. (Hzlca giyinirim.)",
          ],
          [
            "sich ausruhen",
            "dinlenmek",
            "Nach der Arbeit ruhe ich mich aus. (ten sonra dinlenirim.)",
          ],
          ["sich beeilen", "acele etmek", "Beeil dich! (Acele et!)"],
          [
            "sich treffen",
            "bulumak (karlkl)",
            "Wir treffen uns um 6 Uhr. (Saat 6da buluuyoruz.)",
          ],
          [
            "sich konzentrieren (auf + Akkusativ)",
            "e odaklanmak",
            "Ich konzentriere mich auf die Aufgabe. (Greve odaklanyorum.)",
          ],
        ]}
      />

      <Sub title="Edatl kalplar (mit Prpositionen)  anlam farklar" />
      <Explain
        title="Ayn fiil farkl edatla farkl anlam verir"
        lead="Edat + hl kombinasyonu kalptr; birlikte ezberle."
      >
        <p>
          <Term>sich freuen auf + Akkusativ</Term> = gelecekte olacak bir eye
          sevinmek:
          <em> Ich freue mich auf den Urlaub.</em> (Tatile seviniyorum.)
        </p>
        <p>
          <Term>sich freuen ber + Akkusativ</Term> = olmu/olan bir eye
          sevinmek:
          <em> Er freut sich ber das Geschenk.</em> (Hediye iin seviniyor.)
        </p>
        <p>
          <Term>sich erinnern an + Akkusativ</Term> = bir eyi hatrlamak:
          <em> Ich erinnere mich an die Schule.</em>
        </p>
        <p>
          <Term>sich interessieren fr + Akkusativ</Term> = bir eye ilgi
          duymak:
          <em> Sie interessiert sich fr Politik.</em>
        </p>
        <p>
          <Term>sich kmmern um + Akkusativ</Term> = bir eye/greve gz kulak
          olmak:
          <em> Ich kmmere mich um die Kinder.</em>
        </p>
      </Explain>

      <Sub title="Cmle dzeni: nerede durur? (Wortstellung)" />
      <Explain
        title="Bildirim, soru, olumsuz ve emir cmleleri"
        lead="Refleksif zamir (mich/dich/sich/) genelde fiilin hemen yannda durur."
      >
        <p>
          <strong>Bildirim</strong>:<em> Ich wasche mich jetzt.</em> (imdi
          kendimi ykyorum.)
        </p>
        <p>
          <strong>Genel soru (Evet/Hayr)</strong>:<em> Wschst du dich?</em>{" "}
          (Kendini ykyor musun?)
        </p>
        <p>
          <strong>W-sorusu</strong>:<em> Wann triffst du dich mit Ali?</em> (Ali
          ile ne zaman buluuyorsun?)
        </p>
        <p>
          <strong>Olumsuz</strong>:
          <em>
            {" "}
            Ich wasche mich <u>nicht</u>.
          </em>{" "}
          (Kendimi ykamyorum.)
        </p>
        <p>
          <strong>Emir</strong>:<em> Wasch dich!</em> /{" "}
          <em> Waschen Sie sich!</em>
        </p>
      </Explain>

      <Sub title="Ayrlabilen fiillerle birleim (trennbare Verben)" />
      <Explain
        title="Refleksif zamir ortada, ayra sonda"
        lead="nek sonda grnr; refleksif zamir ekimli fiilden sonra gelir."
      >
        <p>
          <em>
            Ich <u>ziehe</u> mich <u>an</u>.
          </em>{" "}
          (Giyiniyorum.)
          <br />
          <em>Ruh dich aus!</em> (Dinlen!)
          <br />
          <em>
            Ich <u>melde</u> mich online <u>an</u>.
          </em>{" "}
          (evrimii kayt oluyorum.)
        </p>
      </Explain>

      <Sub title="Gemi zaman (Perfekt)  dzen" />
      <Explain
        title="Kurulu ve rnekler"
        lead="Genelde haben yardmc fiili kullanlr: haben + sich + Partizip II."
      >
        <p>
          <em>
            Ich <u>habe</u> mich <u>ausgeruht</u>.
          </em>{" "}
          (Dinlendim.)
          <br />
          <em>
            Wir <u>haben</u> uns <u>getroffen</u>.
          </em>{" "}
          (Bulutuk.)
          <br />
          <em>
            Ich <u>habe</u> mich <u>angemeldet</u>.
          </em>{" "}
          (Kayt oldum.)
        </p>
        <Callout type="warn" title="Kk not">
          <p>
            ou dnl fiilde yardmc fiil <Term>haben</Term>dr. Nadiren
            hareket/var anlaml baz kalp fiillerde fark grebilirsin, ancak
            A2de genelde <Term>haben</Term> ile al.
          </p>
        </Callout>
      </Explain>

      <Sub title="Sk hatalar (Hufige Fehler)" />
      <Callout type="error" title="Dikkat et">
        <ul className="compact-list">
          <li>
            <Term>*Ich interessiere fr *</Term> deil,
            <Term> Ich interessiere mich fr </Term>.
          </li>
          <li>
            <Term>*Ich wasche mich die Hnde*</Term> deil,
            <Term> Ich wasche mir die Hnde</Term>.
          </li>
          <li>
            Ayrlabilen fiillerde sra:{" "}
            <em>
              Ich <u>ziehe</u> mich <u>an</u>
            </em>
            , <em>*Ich ziehe an mich*</em> deil.
          </li>
        </ul>
      </Callout>

      <Sub title="rnekler (Beispiele)" />
      <Examples
        items={[
          {
            en: "Ich interessiere mich fr Sprachen.",
            tr: "Dillere ilgi duyuyorum.",
            pron: "ih interesire miH fyr praHn",
          },
          {
            en: "Ruh dich aus!",
            tr: "Dinlen!",
            pron: "ruh diH aus",
          },
          {
            en: "Wir haben uns gestern getroffen.",
            tr: "Dn bulutuk.",
            pron: "vi:r haben uns gestern getrofn",
          },
        ]}
      />

      <Sub title="Mikro Altrmalar  boluu doldur (semeli)" />
      <ExerciseFill
        title="Doru refleksif biimi se"
        items={[
          {
            id: "rv1",
            before: "Ich ___ (sich freuen) auf den Urlaub.",
            after: "",
            answers: ["freue mich"],
            answersOptions: ["freue mich", "freue mir", "freut mich"],
          },
          {
            id: "rv2",
            before: "Er ___ (sich interessieren) fr Politik.",
            after: "",
            answers: ["interessiert sich"],
            answersOptions: [
              "interessiert sich",
              "interessiert ihn",
              "interessiert ihm",
            ],
          },
          {
            id: "rv3",
            before: "Wir ___ (sich treffen) heute Abend.",
            after: "",
            answers: ["treffen uns"],
            answersOptions: ["treffen uns", "treffen wir uns", "treffen euch"],
          },
          {
            id: "rv4",
            before: "Ich ___ (sich die Hnde waschen).",
            after: "",
            answers: ["wasche mir die Hnde"],
            answersOptions: [
              "wasche mir die Hnde",
              "wasche mich die Hnde",
              "wasche mir die Hand",
            ],
          },
          {
            id: "rv5",
            before: "Bitte, ___! (sich setzen  emir)",
            after: "",
            answers: ["setz dich"],
            answersOptions: ["setz dich", "setz dir", "setz du dich bitte"],
          },
          {
            id: "rv6",
            before: "Nach dem Sport ___ ich mich ___ (sich ausruhen).",
            after: "",
            answers: ["ruhe mich aus"],
            answersOptions: ["ruhe mich aus", "ruhen mich aus", "ruhe mir aus"],
          },
        ]}
      />

      <Sub title="Mini Test (Multiple Choice)" />
      <ExerciseMC
        title="Hzl kontrol"
        items={[
          {
            q: "Doru kullanm hangisi?",
            choices: [
              "Ich interessiere fr Musik.",
              "Ich interessiere mich fr Musik.",
              "Ich interessiere mir fr Musik.",
            ],
            correctIndex: 1,
            explain: "Kalp: sich interessieren fr + Akkusativ (i hli).",
          },
          {
            q: "Ayrlabilen fiil ile doru sra hangisi?",
            choices: [
              "Ich anziehe mich.",
              "Ich ziehe mich an.",
              "Ich ziehe an mich.",
            ],
            correctIndex: 1,
            explain:
              "Refleksif zamir ekimli fiilden sonra, nek sonda: ziehe mich an.",
          },
          {
            q: "Ellerimi ykyorum cmlesi hangisi?",
            choices: [
              "Ich wasche mich die Hnde.",
              "Ich wasche mir die Hnde.",
              "Ich wasche die Hnde mich.",
            ],
            correctIndex: 1,
            explain:
              "Sahiplik Dativ + beden paras: mir + die Hnde (Akkusativ nesne).",
          },
          {
            q: "Perfekt doru kullanm hangisi?",
            choices: [
              "Ich habe mich ausgeruht.",
              "Ich bin mich ausgeruht.",
              "Ich habe mir ausgeruht.",
            ],
            correctIndex: 0,
            explain: "Dnl fiiller A2de genelde haben alr.",
          },
        ]}
      />

      <Sub title="Mini yazma (4080 kelime)" />
      <Callout
        type="tip"
        title="Gnlk rutinini anlat  en az 4 dnl fiil kullan"
      >
        <p>
          rn.:{" "}
          <em>Ich stehe auf, dusche mich, rasiere mich, ziehe mich an.</em>{" "}
          Sonra <em>sich freuen auf</em> ve <em>sich interessieren fr</em>{" "}
          kalplaryla iki cmle ekle. Net, ksa, ama doal yaz.
        </p>
      </Callout>
    </>
  );
}
