import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Home() {
  return (
    <div className="had-sub had-sub--home" data-sub="home">
      <section
        className="had-section had-section--wohnort"
        data-toc-title="Wohnort & Umfeld"
      >
        <Explain
          title="Wohnort  in + ehir/lke"
          lead="Trkedeki -de/da eki Almancada ounlukla <Key>in</Key> ile verilir."
        >
          <Formula
            rows={[
              [
                "ehir",
                "Ich wohne in Berlin / Izmir.",
                "de yayorum.",
              ],
              [
                "lke (diil ou lke ad)",
                "Ich wohne in der Trkei.",
                "Trkiyede yayorum.",
              ],
              [
                "lke (oul)",
                "Ich wohne in den Niederlanden.",
                "Hollandada yayorum.",
              ],
              [
                "Semt",
                "Ich wohne im Stadtteil Neuklln.",
                "Neuklln semtinde yayorum.",
              ],
            ]}
          />
          <Callout type="info" title="in der/in den neden var?">
            <p>
              Baz lke adlar dilbilgisel olarak <strong>diil</strong> (
              <em>die Trkei</em>) veya <strong>oul</strong>(
              <em>die Niederlande</em>) kabul edilir; bu yzden{" "}
              <Key>in der</Key>, <Key>in den</Key> grrsn. A1de sadece kalp
              olarak renmen yeterli.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="had-section had-section--strassen"
        data-toc-title="Sokak & numara"
      >
        <Explain
          title="Sokak + numara dizilii"
          lead="Almancada sra genelde Sokak Ad + Numara eklindedir."
        >
          <MiniTable
            head={["Trke mantk", "Almanca yazm", "rnek okunu (TR)"]}
            rows={[
              ["X Sokak, No: 12", "Hauptstrae 12", "hauptrase tsvolf"],
              ["Yol/Meydan + No", "Alexanderplatz 3", "aleksandaplats dray"],
              ["Bulvar + No", "Lindenallee 5", "lindenale faif"],
            ]}
          />
          <Callout type="tip" title="in der  Strae kalb">
            <p>
              X sokanda demek iin <Key>in der  Strae</Key> kullanlr:
              <em>Ich wohne in der Weserstrae.</em> Ksa biim:{" "}
              <em>Weserstrae 19</em>.
            </p>
          </Callout>
        </Explain>

        <Explain title="Ksa diyaloglar" lead="Adres vermeyi pratik gr">
          <Examples
            items={[
              {
                en: "A: Wo wohnst du genau?  B: In der Weserstrae 19.",
                tr: "A: Tam olarak nerede yayorsun?  B: Weserstrae 19da.",
                pron: "vo vohnst du genau  in dea vezertrase noyn-tsen",
              },
              {
                en: "A: In welchem Stadtteil?  B: Im Stadtteil Nippes.",
                tr: "A: Hangi semtte?  B: Nippes semtinde.",
                pron: "in velHm tattayl  im tattayl nippes",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="had-section had-section--wohnform"
        data-toc-title="Ev tipi & kat bilgisi"
      >
        <Explain
          title="Ev tipi, kat ve kap"
          lead="Gerekli kelimeler ve kalplar"
        >
          <MiniTable
            head={["Almanca", "Trke", "Not / Okunu (TR)"]}
            rows={[
              ["die Wohnung", "daire", "vo:nunG"],
              ["das Einfamilienhaus", "mstakil ev", "ayn-familiyen-haus"],
              ["die Etage / der Stock", "kat", "etaje / tok"],
              ["im 3. Stock", "3. katta", "im drittn tok"],
              [
                "die Tr / die Wohnungstr",
                "kap / daire kaps",
                "ta / vo:nunGs-ta",
              ],
              ["die Hausnummer", "ev numaras", "hausnuma"],
              ["die Postleitzahl (PLZ)", "posta kodu", "postlayt-tsal"],
            ]}
          />
          <Examples
            items={[
              {
                en: "Ich wohne im dritten Stock, Wohnung Nummer 8.",
                tr: "3. katta yayorum, daire numaras 8.",
                pron: "ih vone im drittn tok, vo:nunG numea aHt",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
