import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Friends() {
  return (
    <div className="ffh-sub ffh-sub--friends" data-sub="friends">
      <section
        className="ffh-section ffh-section--people"
        data-toc-title="nsanlar tanmlama"
      >
        <Explain
          title="Kii tanmlama  prdikativ sfat"
          lead="A1de isimden sonra yaln sfatla tanmlarz."
        >
          <Formula
            rows={[
              [
                "Kalp",
                "Subjekt + sein + Adjektiv",
                "Er ist freundlich. / Sie ist sportlich.",
              ],
              [
                "Geniletme",
                "sehr/nicht",
                "Sie ist sehr nett. Er ist nicht ruhig.",
              ],
            ]}
          />
          <MiniTable
            head={["Adjektiv", "Trke", "rnek cmle"]}
            rows={[
              ["nett", "kibar", "Sie ist nett."],
              ["ruhig", "sakin", "Er ist ruhig."],
              ["offen", "ak (szl)", "Sie ist offen und hilfsbereit."],
              ["sportlich", "sportif", "Er ist sportlich."],
              ["lustig", "komik", "Er ist lustig."],
              ["freundlich", "gler yzl", "Die Leute sind freundlich."],
            ]}
          />
          <Callout type="warning" title="Hata uyars">
            <p>
              <Key>*Sie ist eine nett</Key> deil {" "}
              <strong>Sie ist nett</strong>. A1de yklem konumunda sfat{" "}
              <Term>ek almaz</Term>.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--meet"
        data-toc-title="Buluma & davet"
      >
        <Explain title="Davet & plan" lead="Basit ve kibar kalplar">
          <MiniTable
            head={["Almanca", "Trke", "Not / Okunu (TR)"]}
            rows={[
              ["Hast du Zeit?", "Vaktin var m?", "has(t) du tsayt"],
              ["Wollen wir ?", " yapalm m?", "voln viA"],
              ["Sollen wir ?", " yapalm m? (neri)", "zoln viA"],
              ["Ich habe keine Zeit.", "Vaktim yok.", ""],
              ["Gute Idee!", "yi fikir!", ""],
              ["Tut mir leid, vielleicht morgen.", "zgnm, belki yarn.", ""],
            ]}
          />
          <Examples
            items={[
              {
                en: "Hast du Zeit am Samstag?  Wollen wir ins Kino gehen?",
                tr: "Cumartesi vaktin var m?  Sinemaya gidelim mi?",
                pron: "has du tsayt am zamstak  voln viA ins kino ge-en",
              },
              {
                en: "Gute Idee! Um 18 Uhr?",
                tr: "yi fikir! Saat 18:00 olur mu?",
                pron: "gute ide  um aHtsen uua",
              },
            ]}
          />
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--smalltalk"
        data-toc-title="Kk konuma (small talk)"
      >
        <Explain
          title="Ksa sohbet allar"
          lead="Basit sorularla sohbeti balat"
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              ["Wie gehts?", "Naslsn?", "vi gets"],
              [
                "Was machst du am Wochenende?",
                "Hafta sonu ne yapyorsun?",
                "vas mahst du am voHenende",
              ],
              [
                "Was sind deine Hobbys?",
                "Hobilerin neler?",
                "vas zint dayn hobis",
              ],
              ["Wo wohnst du?", "Nerede yayorsun?", "vo vohnst du"],
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
