import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function QuantitiesSizes() {
  return (
    <div className="ffh-sub ffh-sub--quantities" data-sub="quantities-sizes">
      <section
        className="ffh-section ffh-section--amount"
        data-toc-title="Miktar & beden"
      >
        <Explain title="Mengen & Gren" lead="Ka tane? Hangi beden?">
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "ein Kilo / ein halbes Kilo",
                "bir kilo / yarm kilo",
                "ayn kilo / ayn halbes kilo",
              ],
              [
                "200 Gramm / ein Pfund",
                "200 gram / yarm kilo",
                "tsvay-hundert gram / ayn pfund",
              ],
              [
                "ein Stck / zwei Stck",
                "bir adet / iki adet",
                "ayn tk / tsvay tk",
              ],
              [
                "Gre S/M/L/XL",
                "Beden S/M/L/XL",
                "grse es / em / el / iks-el",
              ],
              ["Passt das?", "Uyar m?", "past das"],
              [
                "Darf ich das anprobieren?",
                "Deneyebilir miyim?",
                "darf ih das an-probi-ren",
              ],
            ]}
          />
          <Formula
            rows={[
              [
                "stek",
                "Ich mchte 500 Gramm Tomaten.",
                "500 gram domates istiyorum.",
              ],
              [
                "Beden sorma",
                "Haben Sie das in Gre M?",
                "M bedeni var m?",
              ],
              [
                "Uygunluk",
                "Das passt gut / zu gro / zu klein.",
                "iyi / byk / kk",
              ],
            ]}
          />
          <Callout type="info" title="anprobieren (ayrlr)">
            <p>
              <Key>Ich probiere</Key> die Jacke <strong>an</strong>. /{" "}
              <Key>Zieh</Key> die Jacke <strong>an</strong>!
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialog"
        data-toc-title="Diyalog"
      >
        <Explain title="Soyunma kabini" lead="Bedendeneme">
          <Examples
            items={[
              {
                en: "Wo sind die Umkleiden?  Dort links.",
                tr: "Soyunma kabinleri nerede?  Solda.",
                pron: "vo zint di umklaydn  dort links",
              },
              {
                en: "Die Hose ist zu klein. Haben Sie eine Nummer grer?",
                tr: "Pantolon kk. Bir numara by var m?",
                pron: "di hoze ist tsu klayn. habn zi ayn numara grsa",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
