import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function ComparisonsDiscounts() {
  return (
    <div className="ffh-sub ffh-sub--compare" data-sub="comparisons-discounts">
      <section
        className="ffh-section ffh-section--compare"
        data-toc-title="Karlatrma & indirim"
      >
        <Explain
          title="Vergleichen & Rabatte"
          lead="Daha ucuz/daha pahal  kampanya"
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "teuer / gnstig / billig",
                "pahal / uygun / ucuz",
                "toy-a / gndstiH / biliH",
              ],
              [
                "teurer als / billiger als",
                "den daha pahal/ucuz",
                "toyr-a als / bili-ga als",
              ],
              ["am gnstigsten", "en uygun", "am gndsti-sten"],
              [
                "im Angebot / reduziert",
                "indirimde / drlm",
                "im angbot / redootsi-ert",
              ],
              ["zweite gratis", "ikincisi bedava", "tsvayt grtis"],
            ]}
          />
          <Formula
            rows={[
              [
                "Karlatrma",
                "Das ist billiger als jenes.",
                "Bu, tekinden ucuz.",
              ],
              [
                "En ",
                "Dieses Modell ist am gnstigsten.",
                "En uygunu bu model.",
              ],
              [
                "ndirim sorma",
                "Gibt es Studentenrabatt?",
                "renci indirimi var m?",
              ],
            ]}
          />
          <Callout type="tip" title="als / am sten">
            <p>
              <Term>als</Term> = karlatrma; <Term>am sten</Term> = en :{" "}
              <Key>billiger als</Key>, <Key>am billigsten</Key>.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialog"
        data-toc-title="Diyalog"
      >
        <Explain title="Fiyat karlatrma" lead="Ksa rnek">
          <Examples
            items={[
              {
                en: "Dieses ist teurer, aber besser. Das andere ist billiger.",
                tr: "Bu daha pahal ama daha iyi. Dieri daha ucuz.",
                pron: "di-zs ist toya, aba besa. das andre ist bili-ga",
              },
              {
                en: "Ist das reduziert?  Ja, 20% Rabatt.",
                tr: "Bu indirimli mi?  Evet, %20 indirim.",
                pron: "ist das redootsi-ert  ya, tsvantsiH prosent rabat",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
