const ROW_0_20 = [
  ["0", "null", "nul"],
  ["1", "eins", "ayns"],
  ["2", "zwei", "tsvay"],
  ["3", "drei", "dray"],
  ["4", "vier", "fiir"],
  ["5", "fnf", "fnf"],
  ["6", "sechs", "zeks"],
  ["7", "sieben", "ziibn"],
  ["8", "acht", "aHt"],
  ["9", "neun", "noyn"],
  ["10", "zehn", "tsen"],
  ["11", "elf", "elf"],
  ["12", "zwlf", "tsvlf"],
  ["13", "dreizehn", "dray-tsen"],
  ["14", "vierzehn", "fiir-tsen"],
  ["15", "fnfzehn", "fnf-tsen"],
  ["16", "sechzehn", "zekh-tsen"],
  ["17", "siebzehn", "ziip-tsen"],
  ["18", "achtzehn", "aHt-tsen"],
  ["19", "neunzehn", "noyn-tsen"],
  ["20", "zwanzig", "tsvansih"],
];

const ROW_TENS = [
  ["20", "zwanzig", "tsvansih"],
  ["30", "dreiig", "draysih"],
  ["40", "vierzig", "fiirtsih"],
  ["50", "fnfzig", "fnftsih"],
  ["60", "sechzig", "zekstsih"],
  ["70", "siebzig", "ziiptsih"],
  ["80", "achtzig", "aHtsih"],
  ["90", "neunzig", "noyntsih"],
  ["100", "hundert", "hundert"],
  ["1000", "tausend", "tauznt"],
];

export default function Numbers() {
  return (
    <div className="ntd-sub ntd-sub--numbers" data-sub="numbers">
      <section
        className="ntd-section ntd-section--0-20"
        data-toc-title="Zahlen 020"
      >
        <Explain
          title="020: Temel bloklar"
          lead="nce kk saylar ezberlenir; byk saylar bunlardan tretilir."
        >
          <MiniTable
            head={["Say", "Almanca", "Okunu (TR)"]}
            rows={ROW_0_20}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--build"
        data-toc-title="Say kurallar (21+, yz, bin)"
      >
        <Explain
          title="Kurallar  einundzwanzig (21) mant"
          lead="Almanca 21, 31,  ters sral: nce birler, sonra und, sonra onlar."
        >
          <Formula
            rows={[
              [
                "21",
                "ein + und + zwanzig",
                "einundzwanzig",
              ],
              ["34", "vier + und + dreiig", "vierunddreiig"],
              ["58", "acht + und + fnfzig", "achtundfnfzig"],
              ["99", "neun + und + neunzig", "neunundneunzig"],
              ["100", "(ein)hundert", "einhundert / hundert"],
              ["101", "hundert + eins", "einhunderteins (genelde und yok)"],
              ["135", "hundert + fnfunddreiig", "einhundertfnfunddreiig"],
              ["1000", "(ein)tausend", "eintausend / tausend"],
              ["2024", "zweitausendvierundzwanzig", "tek para yazlr"],
            ]}
          />
          <Callout type="tip" title="Telefon & numara okuma">
            <p>
              Telefon numaralar ou zaman <strong>tek tek</strong> ya da{" "}
              <strong>iftli</strong> okunur:
              <em> 0176 45 32</em> {" "}
              <em>null eins sieben sechs, fnfundvierzig, zweiunddreiig</em>.
            </p>
          </Callout>
          <Callout type="info" title="Fiyat & ondalk">
            <p>
              <Key>Virgl</Key> ondalk: <em>3,50 </em> {" "}
              <strong>drei Euro fnfzig</strong>. Nokta kullanma; Almancada
              ondalk = virgl.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--tens"
        data-toc-title="Onlar & yzler"
      >
        <Explain
          title="Onlar & yzler tablo"
          lead="Yazm kk farklara dikkat."
        >
          <MiniTable
            head={["Say", "Almanca", "Okunu (TR)"]}
            rows={ROW_TENS}
          />
        </Explain>
      </section>

      <section
        className="ntd-section ntd-section--mistakes"
        data-toc-title="Sk hatalar"
      >
        <Explain title="Sk hatalar" lead="Ksa dzeltmeler">
          <ul className="ntd-ul ntd-ul--mistakes">
            <li>
              <Key>*zwai</Key> deil  <strong>zwei</strong> <em>(tsvay)</em>.
            </li>
            <li>
              <Key>*dreisig</Key> deil  <strong>dreiig</strong>{" "}
              <em>(dray-sih)</em>.
            </li>
            <li>
              <Key>*neunzig-neun</Key> deil  <strong>neunundneunzig</strong>.
            </li>
          </ul>
          <Examples
            items={[
              {
                en: "Das T-Shirt kostet 19,99 .",
                tr: "Tirt 19,99 .",
                pron: "das tii-rt kostet noyntseyn, noynunnointsih oyro",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
