import React from "react";
import { ExerciseFill, ExerciseMC } from "../../../../../blocks";

export default function HomeAddressDirections_Quiz() {
  return (
    <div className="had-quiz" data-topic="home-address-directions">
      <ExerciseMC
        title="Hzl kontrol  Wohnort & Weg"
        items={[
          {
            q: "Doru adres sras hangisi?",
            choices: [
              "PLZ Ort, Strae Hausnummer",
              "Strae Hausnummer, PLZ Ort",
              "Ort PLZ, Hausnummer Strae",
            ],
            correctIndex: 1,
            explain: "Genel sra: Strae + Hausnummer, sonra PLZ + Ort.",
          },
          {
            q: "Wie komme ich zur Post?   ____ geradeaus, dann links.",
            choices: ["Gehen Sie", "Du gehst", "Fahren wir"],
            correctIndex: 0,
            explain: "Kibar-resmi ynerge: Gehen Sie .",
          },
          {
            q: "Karsnda = ?",
            choices: ["neben", "gegenber", "hinter"],
            correctIndex: 1,
            explain: "gegenber bir yerin karsnda demektir.",
          },
          {
            q: "zur alm nedir?",
            choices: ["zu die", "zu der", "zu dem"],
            correctIndex: 1,
            explain: "zu + der  zur; zu + dem  zum.",
          },
        ]}
      />

      <ExerciseFill
        title="Boluk doldur  Adres & yol"
        items={[
          {
            id: "a1",
            before: "Ich wohne ___ Berlin.",
            after: "",
            answers: ["in"],
            hint: "ehir",
          },
          {
            id: "a2",
            before: "Meine Adresse ist: Weserstrae 19, ___ Berlin.",
            after: "",
            answers: ["12045"],
            hint: "PLZ",
          },
          {
            id: "a3",
            before: "Gehen Sie ___, dann rechts.",
            after: "",
            answers: ["geradeaus"],
            hint: "yn",
          },
          {
            id: "a4",
            before: "Die Apotheke ist ___ der Bank.",
            after: "",
            answers: ["gegenber"],
            hint: "konum",
          },
          {
            id: "a5",
            before: "Knnen Sie das ___?",
            after: "",
            answers: ["buchstabieren"],
            hint: "heceleme",
          },
        ]}
      />
    </div>
  );
}
