// src/pages/Grammar/sections/ger/a2/RelativeClausesIntro.jsx
import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function RelativeClausesIntro() {
  return (
    <>
      <div className="rule-box">
        <strong>lgi Tmceleri (Relativstze  giri)</strong>: Bir ismi
        ayrntyla anlatan ek cmledir. Bu ek cmle <Key>virglden sonra</Key>{" "}
        gelir, <Key>der/die/das</Key> gibi bir <Term>balayc zamir</Term> ile
        balar ve ek cmlenin <Key>ekimli fiili en sonda</Key> olur.
      </div>

      <Explain
        title="lgi tmcesi nedir? (Was ist ein Relativsatz?)"
        lead="Bir kiiyi ya da eyi daha net tanmlamak istediimizde ana cmleye kk bir aklama cmlesi ekleriz."
      >
        <p>
          Trkede yanndaki adam <em>ki</em> Berlinde yayor gibi yaplar
          kurarz. Almancada bu grevi <Term>der/die/das</Term> gibi{" "}
          <strong>balayc zamirler</strong> stlenir:
        </p>
        <p>
          <em>
            Das ist der Mann, <u>der</u> in Berlin <u>wohnt</u>.
          </em>{" "}
          Bu,{" "}
          <strong>
            Berlinde <u>oturan</u>
          </strong>{" "}
          adam.
        </p>
        <p>
          Burada adam kelimesini ek cmle aklyor. Ek cmle virglden sonra
          gelir ve oradaki <strong>ekimli fiil her zaman sonda</strong> olur (
          <em>wohnt</em>).
        </p>
      </Explain>

      <Sub title="Adm adm kurulum (Schritt fr Schritt)" />
      <Explain
        title="Nasl kurarm?"
        lead="Hedef ismi bul  ek cmlenin grevini belirle (zne mi nesne mi?)  doru zamiri se  virgl koy  ek cmlenin fiilini en sona gnder."
      >
        <ol className="compact-list">
          <li>
            <strong>Hedef isim</strong>: Hangi ismi aklamak istiyorsun? rnek:{" "}
            <em>der Mann</em>, <em>die Frau</em>, <em>das Auto</em>.
          </li>
          <li>
            <strong>Grev</strong>: Ek cmlede bu isim <u>zne</u> mi, yoksa{" "}
            <u>nesne</u> mi? zne ise kim? sorusu; nesne ise kimi/neyi?
            sorusu.
          </li>
          <li>
            <strong>Zamir</strong>: zne iin <Term>der/die/das</Term>, nesne
            iin erkek tekilde <Term>den</Term>, dii ve ntrde{" "}
            <Term>die/das</Term>, oulda her iki durumda da <Term>die</Term>.
          </li>
          <li>
            <strong>Fiil en sonda</strong>: Ek cmlenin ekimli fiilini en sona
            koy.
          </li>
        </ol>
        <p>
          Dnm rnei:{" "}
          <em>
            Ich kenne den Mann. Der Mann arbeitet hier.  Ich kenne den Mann,
            <u>der</u> hier <u>arbeitet</u>.
          </em>
        </p>
      </Explain>

      <Sub title="Balayc zamirler: hangi durumda hangisi? (Relativpronomen  Nom./Akk.)" />
      <Explain
        title="Ksa kural"
        lead="Ek cmledeki grev zne ise der/die/das; nesne ise yalnzca erkek tekilde den deiir, dierleri ayndr."
      >
        <p>
          Ek cmle <strong>zne</strong> ise:
          <em> der (m)  die (f)  das (n)  die (pl)</em>. Ek cmle{" "}
          <strong>nesne</strong> ise:
          <em> den (m)  die (f)  das (n)  die (pl)</em>.
        </p>
      </Explain>
      <MiniTable
        head={[
          "Tr",
          "Erkek (der Mann)",
          "Dii (die Frau)",
          "Ntr (das Auto)",
          "oul (die Leute)",
          "Trke ipucu",
        ]}
        rows={[
          [
            "zne (kim?)",
            "der",
            "die",
            "das",
            "die",
            "-en kii/ey anlam: ki -ar",
          ],
          [
            "Nesne (kimi/neyi?)",
            "den",
            "die",
            "das",
            "die",
            "Erkek tekilde den; dierleri ayn",
          ],
        ]}
      />

      <Sub title="Virgl ve fiil en sonda (Komma + Verb am Ende)" />
      <Explain
        title="Neden virgl ve neden fiil sonda?"
        lead="Ek cmle ana cmlenin iine kk bir aklama gibi girer; Almanca ek cmlelerde ekimli fiil her zaman en sonda olur."
      >
        <p>
          <em>
            Das ist die Frau, <u>die</u> im Krankenhaus <u>arbeitet</u>.
          </em>{" "}
          Virgl ek cmleyi ayrr; <em>arbeitet</em> en sonda.
        </p>
        <p>
          <em>
            Ich suche die Tasche, <u>die</u> du gestern <u>gesehen</u> hast.
          </em>{" "}
          Burada Tasche ek cmlede <strong>nesne</strong> oldu; bu yzden{" "}
          <em>die</em> deimedi (dii). Yardmc fiil <em>hast</em> yine en
          sonda.
        </p>
      </Explain>

      <Sub title="rnekler (Beispiele)" />
      <Examples
        items={[
          {
            en: "Das ist der Lehrer, der aus Kln kommt.",
            tr: "Bu, Klnden gelen retmen.",
            pron: "das ist dea leha, dea aus kln komt",
          },
          {
            en: "Ich kenne die Frau, die neben uns wohnt.",
            tr: "Yanmzda oturan kadn tanyorum.",
            pron: "ih kene di frau, di neybn uns vont",
          },
          {
            en: "Das ist das Auto, das ich kaufen will.",
            tr: "Bu, almak istediim araba.",
            pron: "das ist das auto, das ih kaufn vil",
          },
          {
            en: "Ich suche den Mann, den du gestern gesehen hast.",
            tr: "Dn grdn adam aryorum.",
            pron: "ih zuhe den man, den du gestan gezeen hast",
          },
          {
            en: "Wo sind die Leute, die hier arbeiten?",
            tr: "Burada alan insanlar nerede?",
            pron: "vo zint di loyte, di hiya arbaytn",
          },
        ]}
      />

      <Sub title="Sk yaplan hatalar (Typische Fehler)" />
      <Callout type="error" title="Dikkat">
        <ul className="compact-list">
          <li>
            Erkek tekil <strong>nesne</strong> durumunda mutlaka{" "}
            <Term>den</Term> gerekir:
            <em>
              {" "}
              Ich kenne den Mann, <u>den</u> du kennst.
            </em>
          </li>
          <li>
            Ek cmlede <strong>ekimli fiil</strong> her zaman{" "}
            <strong>en sonda</strong>:
            <em>
              {" "}
              , die im Bro <u>arbeitet</u>.
            </em>
          </li>
          <li>
            Yer isimlerinde gnlk dilde bazen <Term>wo</Term> kullanlr:{" "}
            <em>
              Das ist der Ort, <u>wo</u> ich wohne.
            </em>{" "}
            Ancak standart yazda <em>der/die/das</em> tercih edilir:
            <em>
              {" "}
               der Ort, <u>in dem</u> ich wohne.
            </em>{" "}
            Bu seviye iin <strong>der/die/das</strong> ile almak en
            gvenlisidir.
          </li>
        </ul>
      </Callout>

      <Sub title="Dnm altrmas (Haupt cmleleri birletir  ilgi tmcesi)" />
      <ExerciseFill
        title="Cmleleri ilgi tmcesiyle birletir"
        items={[
          {
            id: "rc1",
            before:
              "Ich kenne den Mann. Er arbeitet in Berlin.  Ich kenne den Mann, ___ in Berlin ___.",
            after: "",
            answers: ["der arbeitet", "der in Berlin arbeitet"],
          },
          {
            id: "rc2",
            before:
              "Das ist die Frau. Du suchst die Frau.  Das ist die Frau, ___ du ___ .",
            after: "",
            answers: ["die suchst", "die du suchst"],
          },
          {
            id: "rc3",
            before:
              "Ich brauche das Buch. Das Buch ist neu.  Ich brauche das Buch, ___ neu ___.",
            after: "",
            answers: ["das ist", "das neu ist"],
          },
          {
            id: "rc4",
            before:
              "Ich sehe den Film. Du empfiehlst den Film.  Ich sehe den Film, ___ du ___ .",
            after: "",
            answers: ["den empfiehlst", "den du empfiehlst"],
          },
          {
            id: "rc5",
            before:
              "Wo sind die Leute? Sie warten drauen.  Wo sind die Leute, ___ drauen ___ ?",
            after: "",
            answers: ["die warten", "die drauen warten"],
          },
        ]}
      />

      <Sub title="Boluk doldurma (Zamir seimi + fiil sonda)" />
      <ExerciseFill
        title="der / die / das / den se  fiili en sona gnder"
        items={[
          {
            id: "rc6",
            before: "Kennst du den Lehrer, ___ in Ankara ___ (wohnen)?",
            after: "",
            answers: ["der wohnt", "der in Ankara wohnt"],
          },
          {
            id: "rc7",
            before: "Das ist das Essen, ___ ich gestern ___ (kaufen) habe.",
            after: "",
            answers: ["das gekauft", "das ich gestern gekauft"],
          },
          {
            id: "rc8",
            before: "Ich suche die Frau, ___ du heute ___ (sehen) hast.",
            after: "",
            answers: ["die gesehen", "die du heute gesehen"],
          },
          {
            id: "rc9",
            before: "Wo ist der Zug, ___ nach Mnchen ___ (fahren)?",
            after: "",
            answers: ["der fhrt", "der nach Mnchen fhrt"],
          },
          {
            id: "rc10",
            before:
              "Ich kenne den Mann, ___ du in der Schule ___ (treffen) kannst.",
            after: "",
            answers: ["den treffen", "den du in der Schule treffen"],
          },
        ]}
      />

      <Sub title="Mini Test (Multiple Choice)" />
      <ExerciseMC
        title="Hzl kontrol: doru zamir ve fiil yeri"
        items={[
          {
            q: "Doru cmle hangisi?",
            choices: [
              "Ich kenne den Mann, der arbeitet hier.",
              "Ich kenne den Mann, der hier arbeitet.",
              "Ich kenne den Mann, den arbeitet hier.",
            ],
            correctIndex: 1,
            explain:
              "Ek cmlede ekimli fiil sonda:  der hier arbeitet. den arbeitet yanl grev.",
          },
          {
            q: "Erkek tekil NESNE olduunda hangi zamir?",
            choices: ["der", "den", "das"],
            correctIndex: 1,
            explain:
              "Nesne durumunda yalnzca erkek tekilde den olur; dierleri deimez.",
          },
          {
            q: "Hangi cmlede virgl ve fiil yeri doru?",
            choices: [
              "Das ist die Frau die im Bro arbeitet.",
              "Das ist die Frau, die im Bro arbeitet.",
              "Das ist die Frau, die arbeitet im Bro.",
            ],
            correctIndex: 1,
            explain: "Virgl gerekli ve fiil sonda: , die im Bro arbeitet.",
          },
        ]}
      />

      <Sub title="Telaffuz ipular (Aussprache  Trke odakl)" />
      <Callout type="tip" title="Kk ama etkili ipular">
        <ul className="compact-list">
          <li>
            <Term>der</Term>: dea gibi; <Term>die</Term>: ksa di;{" "}
            <Term>das</Term>: ksa das; <Term>den</Term>: deyn.
          </li>
          <li>
            Virglde ok ksa bir duraklama yap; sonra ek cmleyi doal bir
            akla syle.
          </li>
        </ul>
      </Callout>
    </>
  );
}
