// src/pages/Grammar/sections/ger/a2/AdjectiveEndings.jsx
import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function AdjectiveEndings() {
  return (
    <>
      <div className="rule-box">
        <strong>Sfat Ekleri (Adjektivendungen)</strong>: Sfatn ismin nnde
        kullanld durumlarda sonuna kk ekler gelir. Bu ekler, nce{" "}
        <Key>hangi artikel tr</Key> kullanldna ve sonra{" "}
        <Key>hangi hl (Nominativ, Akkusativ, Dativ)</Key> olduuna gre
        belirlenir.
      </div>

      <Explain
        title="Neden sfat eki var? (Warum Endungen?)"
        lead="Almancada isimlerin cinsiyeti ve hli nemlidir. Bazen bu bilgiyi artikel verir, bazen de sfat stlenir."
      >
        <p>
          Trkede <em>yeni kitap</em> deriz; ekler fiilde veya isimde
          grnr. Almancada ise 
          <em>
            das neu<strong>e</strong> Buch
          </em>
           gibi sfat da kk bir ek alr. Ama, dinleyenin{" "}
          <Term>cinsiyeti</Term> (eril/diil/ntr) ve <Term>oul</Term> ile
          <Term>hli</Term> (Nominativ = zne, Akkusativ = ou nesne, Dativ =
          -e/-a hli) hemen anlamasn salamaktr.
        </p>
        <p>
          Bu yzden nce u sorular sor: Hangi <Key>artikel ailesi</Key> var?{" "}
          <Term>der/die/das</Term> m,
          <Term>ein/kein/benim-senin</Term> gibi mi, yoksa{" "}
          <Term>artikelsiz</Term> mi? Sonra hangi <Key>hl</Key> kullanlyor:
          zne mi, nesne mi, -e/-a m?
        </p>
      </Explain>

      <Sub title=" paket yaklam (Der-Grubu, Ein-Grubu, Artikelsiz)" />
      <Explain
        title="Genel fikir: glzayf paylam"
        lead="Der/die/das gibi belirli artikel bilgiyi zaten tar  sfat ou zaman <b>-e/-en</b>. Artikelsizse bilgiyi sfat tar  daha gl ekler."
      >
        <ul className="compact-list">
          <li>
            <strong>Der-Grubu</strong> (belirli artikel: der/die/das, bu/u gibi
            iaretliler): Artikel zaten hangi hl olduunu gsterir. Sfat ou
            yerde <Term>-e</Term> ya da <Term>-en</Term> alr.
          </li>
          <li>
            <strong>Ein-Grubu</strong> (ein/kein ve iyelik: mein/dein/sein):
            Baz bilgiler eksik olabilir. Sfat bu boluu ksmen doldurur.
          </li>
          <li>
            <strong>Artikelsiz</strong> (hi artikel yok): Bilgiyi sfat tar.
            Bu yzden daha gl ekler grrsn:{" "}
            <em>neuer Mann, neues Auto, neuen Mann, neuer Frau</em>
          </li>
        </ul>
        <Callout type="tip" title="Hzl anahtar (tekil iin pratik ipucu)">
          <p>
            Der-Grubu tekilde: Nominativde genelde <Term>-e</Term>, dierlerinde
            ska <Term>-en</Term>. Ein-Grubu tekilde: Maskulin Akkusativ ve tm
            Dativlerde <Term>-en</Term> ok grnr. Artikelsiz: Sfat ekleri
            daha belirgindir; tabloya bakarak pekitir.
          </p>
        </Callout>
      </Explain>

      <Sub title="Nominativ (zne)  Kim? Ne?" />
      <Explain
        title="zne olduunda tipik kalplar"
        lead="Der-Grubu: ou tekil durumda sfat <b>-e</b>. Ein-Grubu: artikel eksik bilgiyi ksmen tamamlar. Artikelsiz: bilgiyi sfat tar."
      >
        <p>
          zne olduunda basit dn:{" "}
          <em>der neue Mann, die neue Frau, das neue Kind</em>. oulda hem
          Der-Grubu hem de Ein-Grubu:{" "}
          <em>
            die neu<strong>en</strong> Leute
          </em>
          . Artikelsiz zne:{" "}
          <em>neuer Mann, neue Frau, neues Kind, neue Leute</em>.
        </p>
      </Explain>
      <MiniTable
        head={["Kalp", "Maskulin", "Feminin", "Neutrum", "Plural"]}
        rows={[
          [
            "Der-Grubu",
            "der neue Mann",
            "die neue Frau",
            "das neue Kind",
            "die neuen Leute",
          ],
          [
            "Ein-Grubu",
            "ein neuer Mann",
            "eine neue Frau",
            "ein neues Kind",
            "keine neuen Leute",
          ],
          ["Artikelsiz", "neuer Mann", "neue Frau", "neues Kind", "neue Leute"],
        ]}
      />

      <Sub title="Akkusativ (Nesne)  Kimi? Neyi?" />
      <Explain
        title="Nesne olduunda en ok maskeuline dikkat"
        lead="Maskulin Akkusativ belirgindir: der  den; ein  einen; sfat ou zaman <b>-en</b> olur."
      >
        <p>
          <em>
            Ich sehe{" "}
            <u>
              den neu<strong>en</strong> Mann
            </u>
            .
          </em>
          <br />
          <em>
            Ich habe{" "}
            <u>
              einen neu<strong>en</strong> Laptop
            </u>
            .
          </em>
          <br />
          Feminin ve Neutrumda ekil genelde Nominative benzer kalr. oulda{" "}
          <em>
            die neu<strong>en</strong> 
          </em>
          . Artikelsiz Akkusativ maskulin: <em>neuen Mann</em>.
        </p>
      </Explain>
      <MiniTable
        head={["Kalp", "Maskulin", "Feminin", "Neutrum", "Plural"]}
        rows={[
          [
            "Der-Grubu",
            "den neuen Mann",
            "die neue Frau",
            "das neue Kind",
            "die neuen Leute",
          ],
          [
            "Ein-Grubu",
            "einen neuen Mann",
            "eine neue Frau",
            "ein neues Kind",
            "keine neuen Leute",
          ],
          ["Artikelsiz", "neuen Mann", "neue Frau", "neues Kind", "neue Leute"],
        ]}
      />

      <Sub title="Dativ (Ynelme/-e/-a)  Kime? Neye? Nerede?" />
      <Explain
        title="Dativte ou yerde sfat <b>-en</b>"
        lead="Der-Grubu: dem/der/dem + <b>neuen</b> ; Plural: den + <b>neuen</b> Leuten. Ein-Grubu da benzer. Artikelsiz: <b>neuem/neuer/neuem/neuen</b>."
      >
        <p>
          <em>
            mit{" "}
            <u>
              dem neu<strong>en</strong> Freund
            </u>
          </em>
          ,
          <em>
            {" "}
            bei{" "}
            <u>
              der neu<strong>en</strong> Kollegin
            </u>
          </em>
          ,
          <em>
            {" "}
            mit{" "}
            <u>
              dem neu<strong>en</strong> Auto
            </u>
          </em>
          ,
          <em>
            {" "}
            mit{" "}
            <u>
              den neu<strong>en</strong> Leuten
            </u>
          </em>
          . Artikelsiz:{" "}
          <em>
            mit <u>neuem</u> Freund, mit <u>neuer</u> Kollegin
          </em>
          .
        </p>
      </Explain>
      <MiniTable
        head={["Kalp", "Maskulin", "Feminin", "Neutrum", "Plural"]}
        rows={[
          [
            "Der-Grubu",
            "dem neuen Mann",
            "der neuen Frau",
            "dem neuen Kind",
            "den neuen Leuten",
          ],
          [
            "Ein-Grubu",
            "einem neuen Mann",
            "einer neuen Frau",
            "einem neuen Kind",
            "keinen neuen Leuten",
          ],
          [
            "Artikelsiz",
            "neuem Mann",
            "neuer Frau",
            "neuem Kind",
            "neuen Leuten",
          ],
        ]}
      />

      <Sub title="rnekler (Beispiele  ksa balamla)" />
      <Examples
        items={[
          {
            en: "Der neue Lehrer ist freundlich.",
            tr: "Yeni retmen (erkek) arkada canls.",
            pron: "dea noy lea-ra ist froyntliH",
          },
          {
            en: "Ich nehme den neuen Bus.",
            tr: "Yeni otobs alyorum (ona biniyorum).",
            pron: "iH neme den noyn bus",
          },
          {
            en: "Sie spricht mit dem neuen Chef.",
            tr: "Yeni mdrle konuuyor.",
            pron: "zi priHt mit dem noyn ef",
          },
          {
            en: "Wir suchen eine neue Wohnung.",
            tr: "Yeni bir daire aryoruz.",
            pron: "via zuxn ayn noy vo:nung",
          },
          {
            en: "Er arbeitet mit neuen Technologien.",
            tr: "Yeni teknolojilerle alyor.",
            pron: "ea arbaytet mit noyn tek-nolo-gi:n",
          },
        ]}
      />

      <Sub title="Telaffuz ipular (TR odakl)" />
      <Explain
        title="Ksa ynlendirmeler"
        lead="ou ek zayf hece gibi okunur; vurguyu kelimenin kknde tut."
      >
        <ul className="compact-list">
          <li>
            <Term>-en</Term>, <Term>-em</Term>, <Term>-er</Term> ekleri ksa ve
            hzl: <em>noyn, noym, noy</em> gibi.
          </li>
          <li>
            <Term>eu</Term> oy gibi: <em>neu  noy</em>; <em>neue  noy</em>.
          </li>
          <li>
            Uzun nllerde srey koru: <em>Frau</em> frau, <em>Auto</em>{" "}
            auto.
          </li>
        </ul>
      </Explain>

      <Sub title="Boluk doldurma (doru kalb se)" />
      <ExerciseFill
        title="Der/Ein/Artikelsiz + sfat eki"
        items={[
          {
            id: "ae1",
            before:
              "Ich suche ___ ___ Wohnung. (neu, Nominativ/Akkusativ  feminin, Ein-Grubu)",
            after: "",
            answers: ["eine neue"],
            answersOptions: ["eine neue", "ein neues", "einen neuen"],
          },
          {
            id: "ae2",
            before:
              "Wir helfen ___ ___ Nachbar. (freundlich, Dativ  maskulin, Der-Grubu)",
            after: "",
            answers: ["dem freundlichen"],
            answersOptions: [
              "dem freundliche",
              "dem freundlichen",
              "den freundlichen",
            ],
          },
          {
            id: "ae3",
            before:
              "Er hat ___ ___ Laptop gekauft. (teuer, Akkusativ  maskulin, Ein-Grubu)",
            after: "",
            answers: ["einen teuren"],
            answersOptions: ["ein teurer", "einen teuren", "einem teuren"],
          },
          {
            id: "ae4",
            before:
              "___ ___ Kinder spielen im Park. (klein, Nominativ  Plural, Der-Grubu)",
            after: "",
            answers: ["die kleinen"],
            answersOptions: ["die kleinen", "die kleine", "den kleinen"],
          },
          {
            id: "ae5",
            before:
              "Sie fhrt mit ___ ___ Auto. (schnell, Dativ  Neutrum, Artikelsiz)",
            after: "",
            answers: ["schnellem"],
            answersOptions: ["schneller", "schnellem", "schnelles"],
          },
          {
            id: "ae6",
            before:
              "Ich treffe ___ ___ Kollegen. (neu, Akkusativ  maskulin, Der-Grubu)",
            after: "",
            answers: ["den neuen"],
            answersOptions: ["der neue", "den neuen", "dem neuen"],
          },
          {
            id: "ae7",
            before:
              "Er spricht mit ___ ___ Kollegin. (nett, Dativ  feminin, Ein-Grubu)",
            after: "",
            answers: ["einer netten"],
            answersOptions: ["eine nette", "einer netten", "einem netten"],
          },
          {
            id: "ae8",
            before:
              "___ ___ Buch liegt auf dem Tisch. (interessant, Nominativ  Neutrum, Artikelsiz)",
            after: "",
            answers: ["interessantes"],
            answersOptions: ["interessantes", "interessantem", "interessanten"],
          },
        ]}
      />

      <Sub title="oktan semeli  anlam ve kalp" />
      <ExerciseMC
        title="Hzl kontrol"
        items={[
          {
            q: "Aadakilerden hangisi doru? (Akkusativ, maskulin, Der-Grubu)",
            choices: ["der neue Mann", "den neuen Mann", "dem neuen Mann"],
            correctIndex: 1,
            explain:
              "Akkusativ maskulin: der  den, sfat genelde -en: den neuen Mann.",
          },
          {
            q: "Ein-Grubu Dativ maskulin doru olan:",
            choices: [
              "einem neuen Freund",
              "einen neuen Freund",
              "ein neuer Freund",
            ],
            correctIndex: 0,
            explain: "Dativ maskulin: einem neuen Freund.",
          },
          {
            q: "Artikelsiz Nominativ maskulin:",
            choices: ["neuer Lehrer", "neuen Lehrer", "neuem Lehrer"],
            correctIndex: 0,
            explain: "Artikelsiz Nominativ maskulin: neuer Lehrer.",
          },
          {
            q: "Plural Dativ Der-Grubu doru kullanm:",
            choices: ["den neuen Leuten", "die neuen Leuten", "den neue Leute"],
            correctIndex: 0,
            explain:
              "Dativ plural: den + -n ou isimde; sfat -en: den neuen Leuten.",
          },
          {
            q: "Feminin Nominativ Ein-Grubu:",
            choices: ["eine neue Frau", "einen neuen Frau", "einem neuen Frau"],
            correctIndex: 0,
            explain: "Nominativ feminin: eine neue Frau.",
          },
        ]}
      />

      <Sub title="Mini yazma (4080 kelime)  ev/i ilan" />
      <Callout type="tip" title="Sfat eklerini zorunlu kullan">
        <p>
          Kk bir ilan yaz: Dzgn bir i aryorum veya Sessiz bir daire
          aryorum gibi. En az 6 kez isimden nce sfat kullan:{" "}
          <em>
            eine ruhige Wohnung, ein modernes Bro, nette Kollegen, flexible
            Arbeitszeiten
          </em>{" "}
          vb.
        </p>
      </Callout>
    </>
  );
}
