import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function Intro() {
  return (
    <div className="ffh-sub ffh-sub--intro" data-sub="intro">
      <section
        className="ffh-section ffh-section--lead"
        data-toc-title="Einleitung"
        data-modalizable="true"
        data-modal-title="Yeme-me & Sipari"
      >
        <Explain
          title="Neden Food & Drinks?"
          lead="Gnlk hayatta kafede/restoranda en ok gereken kalplar."
        >
          <Formula
            rows={[
              [
                "Kibar istek",
                "Ich htte gern / Ich mchte ...",
                " istiyorum",
              ],
              [
                "Sipari",
                "Fr mich ... , bitte.",
                "Benim iin  ltfen",
              ],
              [
                "Paket/yerinde",
                "Zum Mitnehmen / Zum Hieressen",
                "Paket / burada",
              ],
              ["Hesap", "Zahlen, bitte.", "Hesap ltfen"],
            ]}
          />
          <Callout type="tip" title="Telaffuz mini">
            <MiniTable
              head={["Yaz", "Okunu (TR)", "rnek"]}
              rows={[
                ["u/eu", "oy", "bru / Euro"],
                ["sp/st (bata)", "p/t", "spter / Strae"],
                ["sch", "", "schmeckt"],
              ]}
            />
          </Callout>
        </Explain>

        <Explain title="Mini diyalog" lead="Ksa al">
          <Examples
            items={[
              {
                en: "Die Speisekarte, bitte.",
                tr: "Meny alabilir miyim?",
                pron: "di payz-karte, bitte",
              },
              {
                en: "Ich htte gern einen Kaffee und ein Wasser.",
                tr: "Bir kahve ve bir su istiyorum.",
                pron: "ih hette gern aynn kafe und ayn vasa",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
