import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function MakeAppointment() {
  return (
    <div
      className="ffh-sub ffh-sub--make-appointment"
      data-sub="make-appointment"
    >
      <section
        className="ffh-section ffh-section--call"
        data-toc-title="Randevu alma"
      >
        <Explain
          title="Termin vereinbaren"
          lead="Telefon / resepsiyon kalplar"
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "Ich htte gern einen Termin.",
                "Randevu almak istiyorum.",
                "ih hette gern aynn termin",
              ],
              [
                "Geht es am Montag um 10 Uhr?",
                "Pzt 10 uygun olur mu?",
                "ge-et es am montak um tsen uua",
              ],
              [
                "Passt es Ihnen morgen?",
                "Yarn size uyar m?",
                "past es inn morgen",
              ],
              [
                "Ich kann leider nicht.",
                "Maalesef gelemem.",
                "ih kan layda niHt",
              ],
              [
                "Knnen wir verschieben?",
                "Erteleyebilir miyiz?",
                "knnn viA fea-i-ben",
              ],
              [
                "Ich brauche einen frheren Termin.",
                "Daha erken bir randevu lazm.",
                "ih brauHe aynn fry-eren termin",
              ],
              [
                "Wie ist Ihre Adresse/E-Mail?",
                "Adres/E-posta?",
                "vi ist i-re adres / i-meyl",
              ],
            ]}
          />
          <Formula
            rows={[
              [
                "Tarih & saat",
                "am 12. Mrz, um 9 Uhr",
                "tarih + saat edatlar",
              ],
              [
                "Yer",
                "in der Praxis / bei Dr. Mller",
                "muayenehane / doktor yannda",
              ],
            ]}
          />
          <Callout type="tip" title="Onaylama">
            <p>
              <Key>Alles klar, bis dann!</Key>  Tamam, grmek zere!
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialog"
        data-toc-title="Diyalog"
      >
        <Explain title="Telefonla randevu" lead="Sorucevap">
          <Examples
            items={[
              {
                en: "Guten Tag, ich htte gern einen Termin wegen Zahnschmerzen.",
                tr: "yi gnler, di ars iin randevu almak istiyorum.",
                pron: "guten tak, ih hette gern aynn termin vegen zan-mertsn",
              },
              {
                en: "Geht Dienstag um 9 Uhr?  Ja, das passt. Vielen Dank.",
                tr: "Sal 9 uygun mu?  Evet, olur. Teekkrler.",
                pron: "ge-et diyns-tak um noy-n uua  ya, das past. filen dank",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
