// src/pages/Grammar/sections/ger/a2/TMPWordOrder.jsx
import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";
import Explain from "../../../../../components/Explain";

export default function TMPWordOrder() {
  return (
    <>
      <div className="rule-box">
        <strong>ZAMANEKLYER Dizilii (TemporalModalLokal)</strong>:
        Almancada temel sra ou ana cmlede yledir:{" "}
        <Key>zne + ekimli fiil + Zaman + ekil/Ara/Yntem + Yer</Key>. rnek:{" "}
        <em>
          Ich fahre <u>heute</u> <u>mit dem Bus</u> <u>in die Stadt</u>.
        </em>
      </div>

      <Explain
        title="Neden bu sra nemli? (Warum diese Reihenfolge?)"
        lead="Almanlar bilgiyi doal bir dzende verir: nce ne zaman, sonra nasl/kimle/hangi arala, en sonda nerede/nereye."
      >
        <p>
          Trkede Ben <strong>bugn</strong> <strong>otobsle</strong>{" "}
          <strong>ehre</strong> gidiyorum deriz. Almanca da ayndr:{" "}
          <em>
            Ich fahre <u>heute</u> <u>mit dem Bus</u> <u>in die Stadt</u>.
          </em>{" "}
          Bu dzen cmleyi <strong>akc</strong> ve <strong>doal</strong>{" "}
          yapar.
        </p>
        <ul className="compact-list">
          <li>
            <Term>Zaman</Term> (ne zaman, ne kadar sre, ne sklkla):{" "}
            <em>heute, morgen, oft, drei Stunden, am Abend, um 8 Uhr</em>
          </li>
          <li>
            <Term>ekil / Ara / Yntem</Term> (nasl, hangi arala, kimle):{" "}
            <em>langsam, mit dem Bus, mit Freunden, allein, per E-Mail</em>
          </li>
          <li>
            <Term>Yer</Term> (nerede, nereye, nereden):{" "}
            <em>zu Hause, im Bro, nach Berlin, in die Stadt, von Ankara</em>
          </li>
        </ul>
        <p>
          Zaman ifadesini zellikle vurgulamak istersen cmlenin en bana
          tayabilirsin. O zaman ekimli fiil yine <strong>ikinci ge</strong>{" "}
          kalr:{" "}
          <em>
            <u>Heute</u> <u>fahre</u> ich mit dem Bus in die Stadt.
          </em>
        </p>
      </Explain>

      <Sub title="rneklerle yap (Mit Beispielen verstehen)" />
      <Examples
        items={[
          {
            en: "Ich lerne heute Abend zu Hause.",
            tr: "Bu akam evde ders alyorum.",
            pron: "ih lrne hoyt abnt tsu hauz",
          },
          {
            en: "Wir fahren morgen mit dem Zug nach Kln.",
            tr: "Yarn trenle Klne gidiyoruz.",
            pron: "via farn morgen mit dem tsug nah kln",
          },
          {
            en: "Sie arbeitet oft am Wochenende im Caf.",
            tr: "Hafta sonu sk sk kafede alr.",
            pron: "zi arbeitet oft am voHenende im kafe",
          },
          {
            en: "Heute fahre ich mit Freunden in die Stadt.",
            tr: "Bugn arkadalarla ehre gidiyorum.",
            pron: "hoyt fare ih mit froyndn in di tat",
          },
        ]}
      />

      <Sub title="Paralar tan (Was gehrt wohin?)" />
      <MiniTable
        head={["Blm", "Ne sorusu?", "Tipik rnekler"]}
        rows={[
          [
            "Zaman",
            "Ne zaman?",
            "heute, morgens, am Montag, um 8 Uhr, drei Stunden",
          ],
          [
            "ekil/Ara",
            "Nasl/kimle/neyinle?",
            "langsam, mit dem Auto, mit Freunden, per E-Mail",
          ],
          [
            "Yer",
            "Nerede/Nereye/Nereden?",
            "zu Hause, im Bro, nach Berlin, in die Stadt, aus der Trkei",
          ],
        ]}
      />

      <Sub title="Olumsuzluk nereye gelir? (nicht / kein)" />
      <Explain
        title="Basit yerletirme kurallar"
        lead="kein isimleri olumsuzlar; nicht fiili veya tm cmleyi olumsuzlar ve genelde yklemden nce gelir."
      >
        <ul className="compact-list">
          <li>
            <Term>kein</Term>:{" "}
            <em>
              Ich habe <u>kein</u> Auto.
            </em>{" "}
            {" "}
            <em>
              Sie trinkt <u>keinen</u> Kaffee.
            </em>
          </li>
          <li>
            <Term>nicht</Term>: yer/zaman/zarfn <strong>nnde</strong>{" "}
            olabilir ya da cmle sonunda Partizipten <strong>nce</strong>:{" "}
            <em>
              Ich fahre <u>nicht</u> heute, sondern morgen.
            </em>{" "}
            {" "}
            <em>
              Ich habe <u>nicht</u> gearbeitet.
            </em>
          </li>
        </ul>
        <Callout type="info" title="Doal yerleim">
          Genelde u ak doaldr: <Term>Zaman</Term>  <Term>ekil/Ara</Term>{" "}
           <Term>Yer</Term>. Olumsuzluk gerekli yerde anlam netletirecek
          ekilde konur:{" "}
          <em>
            Ich fahre heute <u>nicht</u> mit dem Bus, sondern mit dem Auto.
          </em>
        </Callout>
      </Explain>

      <Sub title="Cmleyi baa Zaman getirerek kur (Vurgulu zaman)" />
      <Formula
        rows={[
          [
            "Zaman bata",
            "<b>Zaman</b> + <b>ekimli fiil</b> + zne + ekil + Yer",
            "Heute fahre ich mit dem Bus in die Stadt.",
          ],
          [
            "Sade sra",
            "zne + <b>ekimli fiil</b> + Zaman + ekil + Yer",
            "Ich fahre heute mit dem Bus in die Stadt.",
          ],
        ]}
      />

      <Sub title="Sk yaplan hatalar (und dzeltmeleri)" />
      <Callout type="error" title="Dikkat!">
        <ul className="compact-list">
          <li>
            <em>*Ich fahre in die Stadt mit dem Bus heute.*</em>  Doal:{" "}
            <Term>Ich fahre heute mit dem Bus in die Stadt.</Term>
          </li>
          <li>
            <em>*Heute ich fahre*</em>  ekimli fiil ikinci ge olmal:{" "}
            <Term>Heute fahre ich</Term>
          </li>
          <li>
            Birden fazla zaman ifadesi kullanrken geniten dara doru git:{" "}
            <em>am Montag um 8 Uhr</em>. rnek:{" "}
            <Term>Ich arbeite am Montag um 8 Uhr im Bro.</Term>
          </li>
        </ul>
      </Callout>

      <Sub title="Boluk doldurma (ZamanekilYer srasn kur)" />
      <ExerciseFill
        title="Doal sray yaz"
        items={[
          {
            id: "tml1",
            before:
              "Ich fahre ___ ___ ___ . (heute  mit dem Bus  in die Stadt)",
            after: "",
            answers: ["heute mit dem Bus in die Stadt"],
          },
          {
            id: "tml2",
            before:
              "Wir treffen uns ___ ___ ___. (am Abend  im Park  mit Freunden)",
            after: "",
            answers: ["am Abend mit Freunden im Park"],
          },
          {
            id: "tml3",
            before: "Sie arbeitet ___ ___ ___. (oft  im Caf  allein)",
            after: "",
            answers: ["oft allein im Caf", "oft im Caf allein"],
          },
          {
            id: "tml4",
            before: "Heute ___ ___ ___ . (ich  zu Hause  lerne)",
            after: "",
            answers: ["lerne ich zu Hause"],
          },
          {
            id: "tml5",
            before:
              "Er fhrt ___ ___ ___. (morgen  mit dem Auto  nach Berlin)",
            after: "",
            answers: ["morgen mit dem Auto nach Berlin"],
          },
        ]}
      />

      <Sub title="eldiricili Mini Test (Her soruda 3 seenek)" />
      <ExerciseMC
        title="ZAMANEKLYER  Doru cmleyi se"
        items={[
          {
            q: "Doal ve doru sra hangisi?",
            choices: [
              "Ich fahre in die Stadt mit dem Bus heute.",
              "Ich fahre heute mit dem Bus in die Stadt.",
              "Ich fahre mit dem Bus in die Stadt heute.",
            ],
            correctIndex: 1,
            explain:
              "Zaman  ekil/Ara  Yer: heute  mit dem Bus  in die Stadt.",
          },
          {
            q: "Zaman baa al:  fahre ich mit dem Auto nach Kln.",
            choices: [
              "Morgen ich fahre mit dem Auto nach Kln.",
              "Morgen fahre ich mit dem Auto nach Kln.",
              "Ich fahre morgen mit dem Auto nach Kln.",
            ],
            correctIndex: 1,
            explain:
              "Zaman bata ise ekimli fiil ikinci gedir: Morgen <b>fahre</b> ich ",
          },
          {
            q: "Olumsuzluu doru yere koy:",
            choices: [
              "Ich arbeite nicht heute im Bro.",
              "Ich arbeite heute nicht im Bro.",
              "Ich arbeite heute im Bro nicht.",
            ],
            correctIndex: 1,
            explain:
              "Genelde nicht anlam en iyi netletirecek yerde durur: bugn almyorum (yer deil, eylem zaman olumsuz).",
          },
          {
            q: "Doru sray se:",
            choices: [
              "Wir essen im Restaurant heute mit Freunden.",
              "Wir essen heute mit Freunden im Restaurant.",
              "Wir essen mit Freunden im Restaurant heute.",
            ],
            correctIndex: 1,
            explain:
              "Zaman  ekil/Kimle  Yer: heute  mit Freunden  im Restaurant.",
          },
          {
            q: "Hangi cmle fiili ikinci ge yapyor?",
            choices: [
              "Heute ich koche zu Hause.",
              "Heute koche ich zu Hause.",
              "Ich heute koche zu Hause.",
            ],
            correctIndex: 1,
            explain:
              "Zaman bata  ekimli fiil ikinci: Heute <b>koche</b> ich ",
          },
        ]}
      />
    </>
  );
}
