import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function MenuBasics() {
  return (
    <div className="ffh-sub ffh-sub--menu" data-sub="menu-basics">
      <section
        className="ffh-section ffh-section--menu"
        data-toc-title="Men & sipari"
      >
        <Explain title="Speisekarte & Bestellen" lead="Kibar formller">
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "Die Speisekarte, bitte.",
                "Men ltfen.",
                "di payz-karte, bitte",
              ],
              ["Was empfehlen Sie?", "Ne nerirsiniz?", "vas emp-feylen zi"],
              ["Ich htte gern ...", "... istiyorum (kibar).", "ih hette gern"],
              [
                "Fr mich ..., bitte.",
                "Benim iin ..., ltfen.",
                "fa miH ..., bitte",
              ],
              [
                "Zum Hieressen / Zum Mitnehmen",
                "Burada / Paket",
                "tsum hiA-esen / tsum mit-ney-men",
              ],
              [
                "Klein / Mittel / Gro",
                "Kk / Orta / Byk",
                "klayn / mitl / gros",
              ],
              [
                "Still / mit Sprudel (Wasser)",
                "Dz / sodal (su)",
                "til / mit prudel",
              ],
            ]}
          />
          <Formula
            rows={[
              [
                "ecek",
                "Ein Kaffee / eine Cola / ein Wasser",
                "Artikel dikkat",
              ],
              [
                "Sipari",
                "Ich nehme die Nummer 5.",
                "5 numaray alyorum",
              ],
              [
                "Soru",
                "Gibt es eine Tageskarte?",
                "Gnn mens var m?",
              ],
            ]}
          />
          <Callout type="warning" title="Ich will yerine">
            <p>
              A1de kibar kal: <Key>Ich htte gern</Key> / <Key>Ich mchte</Key>
              .
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialog"
        data-toc-title="Diyalog"
      >
        <Explain title="Ksa sipari" lead="Sorusipari">
          <Examples
            items={[
              {
                en: "Was empfehlen Sie?  Die Suppe ist sehr gut.",
                tr: "Ne nerirsiniz?  orba ok iyi.",
                pron: "vas empfeylen zi  di zuppe ist zea gut",
              },
              {
                en: "Fr mich eine Pizza Margherita, bitte.",
                tr: "Benim iin bir Margherita pizza ltfen.",
                pron: "fa miH ayn pizza margetita, bitte",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
