import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function AskingPrice() {
  return (
    <div className="ffh-sub ffh-sub--asking-price" data-sub="asking-price">
      <section
        className="ffh-section ffh-section--phrases"
        data-toc-title="Fiyat sorma"
      >
        <Explain title="Temel kalplar" lead="Ksa ve kibar sorular">
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              ["Wie viel kostet das?", "Bu ne kadar?", "vi fil kostet das"],
              ["Was kostet ...?", "... ka para?", "vas kostet"],
              ["Ist das im Angebot?", "Bu indirimde mi?", "ist das im angbot"],
              [
                "Gibt es das gnstiger?",
                "Daha ucuza var m?",
                "gipt s das gndstiga",
              ],
              [
                "Haben Sie einen Rabatt?",
                "ndirim var m?",
                "habn zi aynn rabat",
              ],
              ["Ich nehme das.", "Bunu alyorum.", "ih neyme das"],
            ]}
          />
          <Formula
            rows={[
              ["Fiyat syleme", "Es kostet 7 Euro 99.", "7,99 "],
              ["Toplam", "Das macht 21 Euro.", "Toplam 21  tutuyor."],
            ]}
          />
          <Callout type="warning" title=" ve Cent syleyi">
            <p>
              <Term>Euro</Term> <Key>oy-ro</Key>, <Term>Cent</Term> 
              <Key>sent</Key>.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialog"
        data-toc-title="Diyalog"
      >
        <Explain title="Hzl diyalog" lead="Soru  cevap">
          <Examples
            items={[
              {
                en: "Wie viel kostet der Apfel?  80 Cent pro Stck.",
                tr: "Elma ne kadar?  Tane 80 sent.",
                pron: "vi fil kostet dea apfl  aHtsiH sent pro tk",
              },
              {
                en: "Ist das im Angebot?  Ja, heute 2 fr 1.",
                tr: "Bu indirimde mi?  Evet, bugn 2 al 1 de.",
                pron: "ist das im angbot  ya, hoyt tsvey fa ayn",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
