import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function TimeFrequency() {
  return (
    <div className="ffh-sub ffh-sub--time-frequency" data-sub="time-frequency">
      <section
        className="ffh-section ffh-section--time"
        data-toc-title="Saat & sklk"
      >
        <Explain title="Uhrzeit & Hufigkeit" lead="Saatler, gnler, sklklar">
          <MiniTable
            head={["Kalp", "Trke", "Not / Okunu (TR)"]}
            rows={[
              ["um 8 Uhr", "saat 8de", "um aHt uua"],
              [
                "am Montag / am Wochenende",
                "Pazartesi / hafta sonu",
                "am montak / am voHenende",
              ],
              ["von ... bis ...", "dan a kadar", "fon  bis "],
              [
                "jeden Tag / morgens / abends",
                "her gn / sabahlar / akamlar",
                "yeden tag / morgns / abends",
              ],
              [
                "immer / oft / manchmal / selten / nie",
                "her zaman / sk / bazen / nadir / hi",
                "imar / oft / manhmal / zelten / ni",
              ],
              ["gegen 18 Uhr", "yaklak 18", "gegn aHtsen uua"],
            ]}
          />
          <Formula
            rows={[
              [
                "Sklk yeri",
                "Ich oft lese abends.",
                "zarflar genelde fiilden sonra",
              ],
              [
                "Zaman  Yer  Fiil",
                "Am Freitag im Bro arbeite ich.",
                "sra kural",
              ],
            ]}
          />
          <Examples
            items={[
              {
                en: "Ich habe von 9 bis 17 Uhr Unterricht.",
                tr: "9dan 17ye kadar dersim var.",
                pron: "ih habe fon noy-n bis zib-tsen uua unter-riHt",
              },
              {
                en: "Wir treffen uns jeden Montag nach der Schule.",
                tr: "Her Pazartesi okuldan sonra buluuyoruz.",
                pron: "viA trefn uns yeden montak nah dea ule",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
