import React from "react";
import { ExerciseFill, ExerciseMC } from "../../../../../blocks";

export default function FoodDrinksOrdering_Quiz() {
  return (
    <div className="ffh-quiz" data-topic="food-drinks-ordering">
      <ExerciseMC
        title="Hzl kontrol  Essen/Trinken"
        items={[
          {
            q: "Kibar sipari hangisi?",
            choices: [
              "Ich will einen Kaffee.",
              "Ich htte gern einen Kaffee.",
              "Gib mir einen Kaffee.",
            ],
            correctIndex: 1,
            explain: "Kibar form: Ich htte gern  / Ich mchte .",
          },
          {
            q: "Paket demek iin?",
            choices: ["Zum Hieressen", "Zum Mitnehmen", "Zum Bezahlen"],
            correctIndex: 1,
            explain: "Paket = Zum Mitnehmen.",
          },
          {
            q: "Hangi cmle doru?",
            choices: [
              "Die Rechnung, bitte.",
              "Rechnung die, bitte.",
              "Bitte Rechnung die.",
            ],
            correctIndex: 0,
            explain: "Doru kalp: Die Rechnung, bitte.",
          },
        ]}
      />

      <ExerciseFill
        title="Boluk doldur  Kalp pratii"
        items={[
          {
            id: "f1",
            before: "___ htte gern eine Suppe.",
            after: "",
            answers: ["Ich"],
            hint: "zne",
          },
          {
            id: "f2",
            before: "___ Mitnehmen oder zum Hieressen?",
            after: "",
            answers: ["Zum"],
            hint: "Paket mi?",
          },
          {
            id: "f3",
            before: "Ohne ___, bitte. (Zwiebel)",
            after: "",
            answers: ["Zwiebeln"],
            hint: "oul",
          },
          {
            id: "f4",
            before: "Zusammen oder ___?",
            after: "",
            answers: ["getrennt"],
            hint: "Ayr",
          },
        ]}
      />
    </div>
  );
}
