import React from "react";
import Explain from "../../../../../../../components/Explain";
import {
  Key,
  Term,
  MiniTable,
  Formula,
  Examples,
  Callout,
} from "../../../../../blocks";

export default function EatDrinkPhrases() {
  return (
    <div className="ffh-sub ffh-sub--phrases" data-sub="eat-drink-phrases">
      <section
        className="ffh-section ffh-section--phrases"
        data-toc-title="Yeme-ime kalplar"
      >
        <Explain
          title="Redemittel: Essen/Trinken"
          lead="Tat, alk/susuzluk, beeni"
        >
          <MiniTable
            head={["Almanca", "Trke", "Okunu (TR)"]}
            rows={[
              [
                "Ich habe Hunger / Durst.",
                "Am / Susadm.",
                "ih habe hungaa / durst",
              ],
              ["Das schmeckt gut/lecker.", "Lezzetli.", "das mekt gut/leka"],
              [
                "s / salzig / sauer / scharf",
                "tatl / tuzlu / eki / ac",
                "zs / zaltsiH / zau-a / arf",
              ],
              [
                "Ich esse/trinke gern ...",
                "... yemekten/imekten holanrm.",
                "ih ese/trinke gern",
              ],
              ["Noch etwas?", "Baka bir ey?", "noh et-vas"],
              ["Guten Appetit!", "Afiyet olsun!", "guten apetit"],
              ["Schmeckts?", "Lezzetli mi?", "mekts"],
            ]}
          />
          <Formula
            rows={[
              [
                "Beeni",
                "Ich mag Kaffee, aber ich trinke lieber Tee.",
                "Kahveyi severim ama ay tercih ederim.",
              ],
              [
                "En sevdiim",
                "Ich esse am liebsten Pasta.",
                "En ok makarna yerim.",
              ],
            ]}
          />
          <Callout type="tip" title="gern / lieber / am liebsten">
            <p>
              <Key>gern</Key> = severim, <Key>lieber</Key> = daha ok tercih,{" "}
              <Key>am liebsten</Key> = en ok.
            </p>
          </Callout>
        </Explain>
      </section>

      <section
        className="ffh-section ffh-section--dialog"
        data-toc-title="Diyalog"
      >
        <Explain title="Masa sohbeti" lead="Ksa rnekler">
          <Examples
            items={[
              {
                en: "Schmeckts?  Ja, sehr lecker!",
                tr: "Lezzetli mi?  Evet, ok lezzetli!",
                pron: "mekts  ya, zea leka",
              },
              {
                en: "Ich trinke lieber Tee als Kaffee.",
                tr: "Kahvedense ay tercih ederim.",
                pron: "ih trinke li-ba te als kafe",
              },
            ]}
          />
        </Explain>
      </section>
    </div>
  );
}
