import React from "react";
import {
  Key,
  Term,
  Formula,
  Examples,
  Callout,
  ExerciseFill,
  ExerciseMC,
  MiniTable,
  Sub,
} from "../../../blocks";

export default function CleftSentences() {
  return (
    <>
      <div className="rule-box">
        <strong>Cleft sentences</strong>: Bilgiyi iki paraya blerek bir geyi
        vurgularz. En yaygnlar: <Key>It-cleft</Key> ve <Key>Wh-cleft</Key>{" "}
        (what-clause).
      </div>

      <Sub title="It-cleft  Vurgulanan ge + be + that/who " />
      <Formula
        rows={[
          [
            "Yap",
            "It + be + [vurgu] + that/who + cmle",
            "It was <u>John</u> who called.",
          ],
          [
            "Zaman",
            "be fiili zamana gre ekimlenir",
            "It <u>is</u> the price that matters.",
          ],
        ]}
      />
      <Callout type="tip" title="Ne zaman kullanlr?">
        zne, nesne, zaman, yer gibi bir ksm ne karmak iin:{" "}
        <em>
          It was <u>yesterday</u> that
        </em>
      </Callout>

      <Sub title="Wh-cleft (Pseudo-cleft)  What ... is ..." />
      <MiniTable
        head={["Desen", "rnek", "Not"]}
        rows={[
          [
            "What + S + V + is + N",
            "What I need is time.",
            "Vurgu ihtiya zerinde",
          ],
          [
            "What + S + be + V-ing + is + N",
            "What were doing is learning.",
            "Sre vurgusu",
          ],
          [
            "All / The thing (that) ... is ...",
            "All I want is coffee.",
            "Snrlama vurgusu",
          ],
        ]}
      />

      <Sub title="Karlatrma: It-cleft vs Wh-cleft" />
      <MiniTable
        head={["Ama", "It-cleft", "Wh-cleft"]}
        rows={[
          [
            "znede vurgu",
            "It was <u>my teacher</u> who helped me.",
            "Who helped me was <u>my teacher</u>.",
          ],
          [
            "Nesnede vurgu",
            "It is <u>this book</u> that I recommend.",
            "What I recommend is <u>this book</u>.",
          ],
          [
            "Fiilde/eylemde vurgu",
            "It was <u>calling</u> that annoyed him.",
            "What annoyed him was <u>calling</u>.",
          ],
        ]}
      />

      <Sub title="rnekler & Mini-Diyalog" />
      <Examples
        items={[
          {
            en: "It was in 1923 that the Republic was founded.",
            tr: "Cumhuriyetin kurulduu yl 1923t (vurgulu).",
            pron: "it woz in nayn-tiin twenti-thrii det d ripablik woz faundd",
          },
          {
            en: "It is the methodology that students struggle with.",
            tr: "rencilerin zorland ey yntemdir.",
            pron: "it iz d metodolc det styudentz stragl wth",
          },
          {
            en: "What motivates me is progress.",
            tr: "Beni motive eden ey ilerlemedir.",
            pron: "wot motiveyts mi iz prog-res",
          },
          {
            en: "All I need is a short break.",
            tr: "Tek ihtiyacm ksa bir mola.",
            pron: "ol ay niid iz e ort breyk",
          },
        ]}
      />
      <Callout type="warn" title="Sk hatalar">
        <ul className="compact-list">
          <li>
            <strong>That/Who seimi</strong>: nsanlar iin <Key>who</Key>,
            dier her ey iin <Key>that</Key>.
          </li>
          <li>
            <strong>ift zne</strong>: <em> It was John he called</em> {" "}
            <strong></strong>{" "}
            <em>
              It was John <u>who</u> called.
            </em>
          </li>
        </ul>
      </Callout>

      <Sub title="Altrma  Gap-fill" />
      <ExerciseFill
        title="It-cleft & Wh-cleft"
        items={[
          {
            id: "cl1",
            before: "___ was last night ___ we finished the project.",
            after: "",
            answers: ["It, that"],
          },
          {
            id: "cl2",
            before: "What I admire most ___ her honesty.",
            after: "",
            answers: ["is"],
          },
          {
            id: "cl3",
            before: "It ___ my brother ___ taught me English.",
            after: "",
            answers: ["was, who"],
          },
          {
            id: "cl4",
            before: "All I want ___ a fair chance.",
            after: "",
            answers: ["is"],
          },
          {
            id: "cl5",
            before: "What matters most ___ consistency.",
            after: "",
            answers: ["is"],
          },
        ]}
      />

      <ExerciseMC
        title="Mini Test"
        items={[
          {
            q: "Nesneyi vurgulamak iin en doal it-cleft?",
            choices: [
              "It is I recommend this book.",
              "It is this book that I recommend.",
              "This book it is that I recommend.",
            ],
            correctIndex: 1,
            explain: "It + be + [nesne] + that + cmle gvdesi.",
          },
        ]}
      />
    </>
  );
}
